<template>
  <div>
    <base-header type="gradient" class="pb-7 pt-7">
    </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-around">
        <div class="col-md-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: CategoriesPage }"   class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>

        <div class="col-md-12 mt-6 mb-8">
          <div class="row no-gutters justify-content-around">
            <div class="col-12">
                <add-tagname class="text-default" :hostApi="hostApi"></add-tagname>
            </div>
            <div class="col-12 mt-4">
              <add-taghirarchy title="Light Table" :hostApi="hostApi"></add-taghirarchy>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AddTagname from "./AddTagName";
import AddTaghirarchy from "./AddTagHirarchy";

export default {
  name: "Add Categories",
  components: {
    AddTagname,
    AddTaghirarchy
  },
  props:{
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    }
  },
  data() {
    return {
      CategoriesPage: ''
    }
  },
  mounted(){
    if(this.source == "modicare"){
      this.CategoriesPage = "Categories"
    }else{
      this.CategoriesPage = this.source+"Categories"
    }
  }
};
</script>

