<template>
    <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            View All Offer
          </h3>
        </div>
        <div class="col text-right">
          <base-button type="info"  size="sm">Add Offer</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Offer Name</th>
          <th>offer_text</th>
          <th>File Type</th>
          <th>Offer File</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.offer_name }}</span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.offer_text }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.file_type }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.offer_file }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                Action
            </div>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
    name: "ViewOfferTable",
    props: {
        type: {
        type: String,
        },
        title: String,
        source: {
        type: String,
        default: "modicare"
        },
        hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
        }
  },
  data(){
    return {
        tableData:[],

    }
  },
  async mounted() {
    let bv_lists = await axios.get(this.hostApi+'/api/view-all-offer/');
    this.tableData = bv_lists.data;
  },
  methods: {
    async ChangeStatus(id, status){
        const data = JSON.stringify({
            status: status,
        });
        const config = {
            method: 'patch',
            url: this.hostApi+'/api/bv-requests/'+id+'/',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : data
        };
        axios(config).then((response) =>{
            this.tableData = this.tableData.map(p =>p.id === id
            ? { ...p, status: response.data.status }
            : p
            );
            this.$toast.success(`BV Requested Successfully ${status}`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
        }).catch((error) =>{
                this.$toast.error(`${error.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        )
    },
    addOffer(){
      if(this.source === "modicare"){
          this.$router.push('/addbrand');
        }
        else{
          this.$router.push(`/${this.source.toLowerCase()}/addbrand`);
        }
    }
  }
}
</script>