<template>
  <div class="add-section">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i
              class="fas fa-chevron-left text-info"
              style="outline: 0; box-shadow: none !important"
            >
            </i>
            <router-link
              :to="{ name: VideoSection }"
              class="pl-2 text-info form-control-label"
              style="font-size: 16px"
              >Back</router-link
            >
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">
          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add New Section</h3>
                  </div>
                </div>
              </div>
            </div>
            <form v-on:submit="AddVideoData">
              <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Title
                          </label>
                          <input 
                            type="text" 
                            class="form-control"
                            placeholder="Video Title" 
                            name="title" 
                            v-model="title" 
                            required
                          />
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section
                          </label>
                          <select 
                            class="form-control form-control-alternative"
                            v-model="section"
                          >
                          <option disabled value="">Select Section</option>
                          <option 
                            v-for="item in Object.keys(section_datas)"
                            :key="item"
                            :value="item">{{item}}</option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>  
                <div class="">
                  <div class="row">
                    <div class="col-6">
                      <label class="form-control-label">
                        Video Link (Youtube/Vimeo)
                      </label>
                      <input 
                        type="text" 
                        class="form-control"
                        placeholder="Video link" 
                        name="video_link" 
                        v-model="video_link" 
                        required
                      />
                    </div>
                    <div class="col-6">
                      <label class="form-control-label">
                        Video Embedded Link (Youtube/Vimeo)
                      </label>
                      <textarea 
                        class="form-control"
                        v-model="embed_link"
                        name="embed_link"
                        required
                      />
                    </div>
                  </div>
                </div>
                <div class="">
                  <div class="row">
                    <div class="col-6">
                      <label class="form-control-label">
                        Status
                      </label>
                      <select 
                        class="form-control form-control-alternative"
                        v-model="status"
                      >
                      <option disabled value="">Select Status</option>
                      <option value="true">True</option>
                      <option value="false">False</option>
                      </select>
                    </div>
                  </div>
                </div>
              </div>

              <div class="card-footer">
                <div class="bg-white border-0">
                  <div class="row">
                    <div class="col-12 text-right">
                      <button
                        type="button"
                        class="btn btn-secondary mr-2"
                        @click="resetForm()"
                      >
                        Cancel
                      </button>
                      <button
                        type="submit"
                        class="btn btn-info"
                      >
                        Add Video Data
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
input {
  color: rgba(122, 23, 117, 0.616);
  font-weight: bold;
}
.card-img-top {
  max-height: 172px;
}
.card-img-top {
  min-height: 172px;
}
</style>
<script>
import { addVideoData } from "../../services/apiHandler";
export default {
  name: "add video",
  props: {
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST,
    },
    source: {
      type: String,
      default: "modicare",
    },
  },
  data() {
    return {
      title: '',
      video_link: '',
      embed_link: '',
      section: '',
      status: '',
      section_datas: [],
      VideoSection: '',
    };
  },
  async created() {
    this.section_datas = this.$store.state.section_datas;
  },
  methods: {
    
    async AddVideoData(e) {
      e.preventDefault();
      let {
        title,
        video_link,
        embed_link,
        section,
        status,
      } = this;
      if (
        !title ||
        !video_link ||
        !embed_link ||
        !section ||
        !status
      ) {
        this.$toast.error(`Please fill all the required field`, {
          position: "top",
          canTimeout: true,
          duration: 2500,
        });
      }
      try{
        const formData = {
          title,
          video_link,
          embed_link,
          section,
          status,
        };
        await addVideoData(this.hostApi, formData);
        this.$toast.success(`Video data added successfully!`, {
            position: "top",
            canTimeout: true,
            duration: 2500,
          });
          this.$router.push({name: this.VideoSection })
      }catch (e) {
        this.$toast.error(`${e.response.data.detail || e.message}`, {
          position: "top",
          canTimeout: true,
          duration: 2500,
        });
      }
    },
    resetForm(){
      this.title="",
      this.video_link="",
      this.embed_link="",
      this.section="",
      this.status=""
    }
  },
  mounted() {
    if (this.source === "modicare") {
      this.VideoSection = "VideoDatas";
    } else {
      this.VideoSection = this.source + "VideoDatas";
    }
  },
};
</script>