import axios from "axios";
const access_token = localStorage.getItem('authentication_token');

export const getCountData = async (host) => {
    const headers = {
        'Authorization': 'token ' + access_token
    };
    return await axios.get(host +'/api/count/', {headers});
}

export const getUsersData = async (host) => {
    return await axios.get(host +'/api/app-user/');
}

export const getBvRequestData = async (host) => {
    const headers = {
        'Authorization': 'token ' + access_token
    };
    return await axios.get(host +'/api/bv-admin-requests/', {headers});
}

export const changeBvRequest = async (host, bv_id, status) => {
    const headers = {
        'Authorization': 'token ' + access_token,
        'Content-Type': 'application/json'
    };
    const data = JSON.stringify({
        status
    })
    return await axios.patch(`${host}/api/bv-requests/${bv_id}/`, data, {headers});
}

export const getNextData = async (next_url) => {
    return await axios.get(next_url);
}

export const getPrevData = async (prev_url) => {
    return await axios.get(prev_url);
}

export const jumpToPage = async (page_url, auth=false) => {
    const headers = {};
    if(auth){
        headers['Authorization'] = 'token ' + access_token
    }
    return await axios.get(page_url, {headers});
}

