export const getSectionData = async (host) => {
    return await axios.get(`${host}/api/section/`);
}

export const addSectionData = async (host, data) => {
    const formData = new FormData();
    formData.append("section_image", data.image_data);
    formData.append("section_type", data.section_type);
    formData.append("title", data.section_title);
    formData.append("section_name", data.section_name);
    formData.append("status", data.section_status);
    formData.append("section_data", data.section_data_url);
    const headers = { 
        'Content-Type': 'multipart/form-data',
        'Authorization': 'token ' + access_token
    }
    return await axios.post(`${host}/api/section/`, formData, {headers});
}

export const editSectionData = async (host, sec_id, data, image_process) => {
    const formData = new FormData();
    if(image_process){
        formData.append("section_image", data.image_data);
    }
    formData.append("section_type", data.section_type);
    formData.append("title", data.section_title);
    formData.append("section_name", data.section_name);
    formData.append("status", data.section_status);
    const headers = { 
        'Content-Type': 'multipart/form-data',
        'Authorization': 'token ' + access_token
    }
    return await axios.patch(`${host}/api/section/${sec_id}/`, formData, {headers});
}

