<template>
    <div class="add-brand">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: CategoriesPage }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-12">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Edit Categories</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Category
                          </label>
                         <input
                              type="text"
                              placeholder="Category Name"
                              v-model="CategoryTag"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                              required
                          />
                       </div>
                    </div>
                     <div class="col-6">
                        <div class="form-group has-label">
                            <label class="form-control-label">
                             TagImage
                          </label>
                          <div class="card">
                            <div v-if="tagImage">
                              <img class="card-img-top" :src="tagImage" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file" type="file" v-on:change="handleImage()" accept="image/*"/>
                              <button type="button" v-if="!tagImage" class="btn btn-primary btn-sm" @click="$refs.file.click()">Add</button>
                              <button type="button" v-else class="btn btn-primary btn-sm" @click="$refs.file.click()">Change</button>
                              <button type="button" :disabled="!tagImage" class="btn btn-info btn-sm" @click="removeImage()">Remove</button>
                            </div>
                          </div>
                        </div> 
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             SubCatgory
                          </label>
                            <select multiple class="form-control form-control-alternative" name="sub_cats[]" v-model="SubCatgory">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in tag_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.tagName }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                      <label class="form-control-label">
                             Serial Number
                          </label>
                         <input
                              type="number"
                              placeholder="Serial Number"
                              v-model="serial_number"
                              ref="serialnum"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                          />
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="CancelForm()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="updateTagHirarchy()">Update Category</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
      max-height: 170px;
      min-height: 170px;
  }
</style>
<script>
import axios from "axios";

var axiosDefaults = require("axios/lib/defaults");
axiosDefaults.xsrfCookieName = "csrftoken"
axiosDefaults.xsrfHeaderName = "X-CSRFToken"
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default({
    name:"Edit TagHirarchy",
    props: {
      source: {
        type: String,
        default: "modicare"
      },
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      }
    },
    data() {
       return{
           click:0,
           Category:'',
           CategoryTag:'', 
           category_tags:'',
           SubCatgory:[],
           tag_lists: [],
           tagImage:'',
           catTagId: '',
           serial_number:'',
           update_catTag:false,
           CategoriesPage: ''
       } 
    },
    async mounted(){
      if(this.source == "modicare"){
        this.CategoriesPage = "Categories"
      }else{
        this.CategoriesPage = this.source+"Categories"
      }
      let id = this.$route.params.id;
      let category = await axios.get(this.hostApi+`/api/tagHirarchy/${id}/`);
      this.tagImage = category.data.category.tagImage;
      this.catTagId = category.data.category.id;
      this.CategoryTag = category.data.category.tagName;
      this.serial_number = category.data.category.serial_no;
      let allTags = await axios.get(this.hostApi+`/api/tags/`);
      let allCategories = await axios.get(this.hostApi+`/api/tagHirarchy/`);
      // const tags = new Set(allTags.data.map(item=>item.tagName))
      const cats = new Set(allCategories.data.map(item=>item.category.tagName))
      let res = [...allTags.data.filter(item=>!cats.has(item.tagName))]
      this.tag_lists = res;
      this.SubCatgory = category.data.subcategory.map(item=>item.id);
    },
    methods:{
        async updateTagHirarchy(){
          await this.updateCatTag();
          if(!this.update_catTag || !this.CategoryTag)return;
           var data = JSON.stringify({
                "tagHirarchy_id":this.$route.params.id,
                "category": this.CategoryTag,
                "subCategory":this.SubCatgory
            });
            var config = {
              method: 'put',
              url: this.hostApi+'/api/add/tagHirarchies/',
              headers: { 
                  'Content-Type': 'application/json',
              },
              data : data,
              xsrfHeaderName: "X-CSRFToken",
            };
            let res = await axios(config);
            if(res.status == 200){
              this.$toast.success(`tag hirarchy updated successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            }else{
              this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
            }
        },
        async handleImage(){
          let imageFile = this.$refs.file.files[0];
          this.tagImage = URL.createObjectURL(imageFile)
        },
        async removeImage(){
            this.tagImage = "";
        },
        async updateCatTag(){
          try{
            if(!this.serial_number){
              this.$refs.serialnum.style.backgroundColor = 'red';
              return;
            }
            let t_id = this.catTagId;
            var formData = new FormData();
            formData.append('tagName', this.CategoryTag);
            formData.append('serial_no', this.serial_number);
            if(this.$refs.file.files[0]){
              formData.append('tagImage', this.$refs.file.files[0]);
            }
            var config = {
                method: 'put',
                url: this.hostApi+'/api/tags/'+ t_id +'/',
                headers: { 
                   'Content-Type': 'multipart/form-data'
                },
                data:formData,
                xsrfHeaderName: "X-CSRFToken",
            }
            let res = await axios(config);
            if(res.status == 200){
                this.update_catTag = true;
                this.$toast.success(`Tag Data Updated successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.$router.push({name:this.CategoriesPage})
            }else{
                this.$toast.warning(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            }
          }catch(e){
            this.$toast.warning(`Something went wrong, ${e}`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            })
          }
        },
        CancelForm(){
          this.$router.push({name:'Categories'})
        }
    }
})
</script>
