<template>
    <div class="add-brand">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-7 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: BackUser }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-7 col-md-12">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">User Coin Data</h3>
                  </div>
                </div>
              </div>
            </div>
          <form @submit.prevent="addUserCoin"> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             User Coin Balance
                          </label>
                          <input
                              type="number"
                              placeholder="Coin Balance"
                              v-model="coin_bal"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                              disabled="disabled"
                          />
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Coin to Add
                          </label>
                          <input
                              type="number"
                              placeholder="Coins"
                              v-model="coin"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                              required
                          />
                       </div>
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="submit" class="btn btn-info">Add Coin</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          <hr />
          <UserBV :hostApi="hostApi" :source="source" :userBv="user_bv" :userId="user_id"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";
import UserBV from "./UserBv";
 export default({
    name: "User Coin",
    components:{
      UserBV
    },
     props: {
      source: {
        type: String,
        default: "modicare"
      },
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      }
    },
    data() {
        return {
            BackUser: "",
            coin_bal: 0,
            coin: 0,
            user_bv:0,
            user_id: this.$route.params.id
        }
    },
    mounted(){
        if(this.source === "modicare"){
            this.BackUser = "Users";
        }else{
            this.BackUser = this.source+"Users";
        }
    },
    async created() {
        let userBvData = await axios.get(this.hostApi+'/api/' + this.$route.params.id + '/bv-coin/');
        if(userBvData.data.status === "ok"){
            setInterval(()=> {
            this.coin_bal = userBvData.data.coin;
            },1000)
            this.user_bv = userBvData.data.bv;
        }
    },
    methods: {
        async addUserCoin(){
            if(this.coin == 0) return;
            var data = JSON.stringify({
                "coin": parseInt(this.coin),
                "user": parseInt(this.$route.params.id),
                "section_type": "teamsbuilders_bonus"
            });

            var config = {
                method: 'post',
                url: this.hostApi+'/api/add/coin/',
                headers: { 
                    'Content-Type': 'application/json',
                },
                data : data
            };
            let res = await axios(config);
            if(res.status == 200){
               this.$toast.success(`User Coin added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.coin = 0;
            this.coin_bal = res.data.coin;
            }else{
                this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
            }
        },
        resetForm(){
            this.coin = 0;
        }
    }
 })
</script>