<template>
    <div class="row">
        <div class="col-6">
            <div class="form-group has-label">
                <label class="form-control-label">
                    {{label}}
                </label>
                <input type="text" class="form-control" placeholder="Description" name="description" v-model="formvalue">
            </div>
        </div>
        <div class="col-4 text-right pt-4">
            <button type="button" class="btn btn-info" v-on:click="UpdateFunc($event, formvalue, index)">Update</button>
            <button type="button" class="btn btn-danger" v-on:click="DeleteFunc($event, index)">Delete</button>
        </div>
    </div>
</template>
<script>
export default {
    name:"InputField",
    props:{
        label:{
            type: String
        },
        inputData:{
            type: String
        },
        index:{
            type: Number
        },
        UpdateFunc:{
            type: Function
        },
        DeleteFunc:{
            type: Function
        }
    },
    data(){
        return {
            formvalue: this.inputData
        }
    }
}
</script>