<template>
<div>
    <div v-if="!showAdd" class="row" v-on:click="enableAddition">
        <div  class="col-8 text-right">
            <button type="button" class="btn btn-primary">Add {{headline}}</button>
        </div>
    </div>
    <div v-else class="row">
        <div class="col-6">
            <div class="form-group has-label">
                <label class="form-control-label">
                    {{headline}} 
                </label>
                <input type="text" class="form-control" :placeholder="`New `+ headline" v-model="newData">
            </div>
        </div>
        <div class="col-4 text-right pt-4">
            <button type="button" class="btn btn-primary" v-on:click="addFunc($event, newData)">Add</button>
            <button type="button" class="btn btn-secondary" v-on:click="enableAddition">Cancel</button>
        </div>
    </div>
</div>
</template>
<script>
export default {
    name: "AddData",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
      headline: {
        type: String,
        default:"Testing"
      },
      addFunc:{
        type: Function
      },
      showAdd:{
        type: Boolean,
        default: false
      },
      enableAddition:{
        type: Function
      }
    },
    data(){
        return {
            newData: ""
        }
    }
}
</script>