 <template>
    <div class="card shadow bg-secondary">
    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row align-items-center">
            <div class="col">
            <h3 class="mb-0">Credit Section Instructions</h3>
            </div>
        </div>
        </div>
    </div>
        <div class="card-body">
        <div class="">
            <div class="row">
                <div class="col-8">
                    <div class="form-group has-label">
                    <label class="form-control-label">
                        Marquee Text in Credit Section
                    </label>
                    <input type="text" class="form-control" placeholder="Marquee Instructions" name="required_coins" v-model="formData.marquee.instruction">
                    </div>
                </div>
                <div class="col-4 text-right pt-4">
                    <button type="button" class="btn btn-info" v-on:click="updateInstruction($event, formData.marquee.instruction, formData.marquee.id, 'marquee')">Update</button>
                </div>
            </div>
            <hr />
            <div v-for="(item, index) in formData.instruction" :key="index">
                <InputField :label="`Instruction `+index" :inputData="item.instruction" :index="item.id" :UpdateFunc="updateInstruction" :DeleteFunc="DeleteInstruction" />
            </div>
            <AddData headline="Instruction" newData="newInstruction" :addFunc="AddInstruction" :showAdd="addInstruction" :enableAddition="AddInstructionToggle"/>
        </div>
        </div>
    </div>
</template>
<script>
import AddData from "../../common/newData.vue";
import InputField from "../../common/inputField.vue";
import { addCreditInstruction, deleteCreditInstruction, editCreditInstruction, getCreditSectionText } from "../../services/apiHandler";
export default {
    name: "Instructions",
    components:{
        AddData,
        InputField
    },
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
    },
    data() {
        return {
            formData : {
               marquee: {},
               instruction: []
            },
            addInstruction: false,
            newInstruction: '',
        }
    },
    async mounted() {
        const instructions = await getCreditSectionText(this.hostApi)
        this.formData = instructions.data.response;
    },
    methods: {
        AddInstructionToggle(){
            this.addInstruction = !this.addInstruction;
        },
        updateInstruction(event, instruction, id, type="instruction"){
            event.target.disabled = true;
            event.target.innerText = "Updating..."
            editCreditInstruction(this.hostApi, id, {instruction, type}).then((response) => {
                if(type == "marquee"){
                    this.formData.marquee = response.data;
                }
                this.$toast.success(`Instruction data updated`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            }).catch((err)=>{
                this.$toast.error(`${err.response.data.message || err.response.data.detail}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            })
        },
        AddInstruction(event, instruction){
            event.target.disabled = true;
            event.target.innerText = "Adding..."
            const form_data ={
                instruction: instruction,
                type: "instruction",
            }
            addCreditInstruction(this.hostApi, form_data).then((res)=>{
                this.formData.instruction.push(res.data);
                 this.$toast.success(`Instruction added`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Add"
                this.AddInstructionToggle();
            }).catch((err)=>{
                this.$toast.error(`${err.response.data.message || err.response.data.detail}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Add"
            })
        },
        DeleteInstruction(event, inst_id){
             this.$swal({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) =>{
                if(result.isConfirmed){
                    event.target.disabled = true;
                    event.target.innerText = "Deleting..."
                    deleteCreditInstruction(this.hostApi, inst_id).then(()=>{
                        this.formData.instruction = this.formData.instruction.filter((obj)=>{return obj.id !== inst_id;})
                        this.$toast.success(`Instruction Deleted Successfully`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    }).catch((err)=>{
                        event.target.disabled = false;
                        event.target.innerText = "Delete"
                        this.$toast.error(`${err.response.data.detail || err.response.data.message}`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    })
                }
            })
            
        }
    }
}
</script>