<template>
  <div class="add-section">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-12 mt-2">
          <span>
            <i
              class="fas fa-chevron-left text-info"
              style="outline: 0; box-shadow: none !important"
            >
            </i>
            <router-link
              :to="{ name: VideoSection }"
              class="pl-2 text-info form-control-label"
              style="font-size: 16px"
              >Back</router-link
            >
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center">
        <div class="col-lg-10 col-md-10">
          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">View/Edit Video Data</h3>
                  </div>
                </div>
              </div>
            </div>
            <form v-on:submit="UpdateVideoData">
              <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Title
                          </label>
                          <input 
                            type="text" 
                            class="form-control"
                            placeholder="Video Title" 
                            name="title" 
                            v-model="title" 
                            required
                          />
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section
                          </label>
                          <select 
                            class="form-control form-control-alternative"
                            v-model="section"
                          >
                          <option disabled value="">Select Section</option>
                          <option 
                            v-for="item in Object.keys(section_datas)"
                            :key="item"
                            :value="item">{{item}}</option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>  
                <div class="">
                  <div class="row">
                    <div class="col-6">
                      <label class="form-control-label">
                        Video Link (Youtube/Vimeo)
                      </label>
                      <input 
                        type="text" 
                        class="form-control"
                        placeholder="Video link" 
                        name="video_link" 
                        v-model="video_link" 
                        required
                      />
                    </div>
                    <div class="col-6">
                      <label class="form-control-label">
                        Video Embedded Link (Youtube/Vimeo)
                      </label>
                      <textarea 
                        class="form-control"
                        v-model="embed_link"
                        name="embed_link"
                        required
                      />
                    </div>
                  </div>
                </div>
                <div class="">
                  <div class="row">
                    <div class="col-6">
                      <label class="form-control-label">
                        Status
                      </label>
                      <select 
                        class="form-control form-control-alternative"
                        v-model="status"
                      >
                        <option disabled value="">Select Status</option>
                        <option value="true">True</option>
                        <option value="false">False</option>
                      </select>
                    </div>
                    <div class="col-6 mt-5">
                      <label class="form-control-label">
                        Thumbnail
                      </label>
                        <img alt="Image placeholder" class="img-responsive img-thumbnail" :src="thumbnail" />
                    </div>
                  </div>
                </div>
                <div class="">
                    <div class="row">
                        <div v-html="embed_link" class="col-6">
                        </div>
                    </div>
                </div>
              </div>

              <div class="card-footer">
                <div class="bg-white border-0">
                  <div class="row">
                    <div class="col-12 text-right">
                      <button
                        type="button"
                        class="btn btn-secondary mr-2"
                        @click="cancelForm()"
                      >
                        Cancel
                      </button>
                      <button
                        type="submit"
                        class="btn btn-info"
                      >
                        Update Video Data
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
input, select, textarea {
  color: rgba(122, 23, 117, 0.616);
  font-weight: bold;
}
.card-img-top {
  max-height: 172px;
}
.card-img-top {
  min-height: 172px;
}
</style>
<script>
import axios from "axios";
import { editVideoData } from "../../services/apiHandler";
export default {
    name: "View/Update Video",
    props: {
        hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST,
        },
        source: {
        type: String,
        default: "modicare",
        },
    },
    data() {
      return {
        title: '',
        video_link: '',
        embed_link: '',
        section: '',
        status: '',
        thumbnail: '',
        section_datas: [],
        VideoSection: '',
      };
  },
  async created() {
    this.section_datas = this.$store.state.section_datas;
  },
  async mounted() {
    if (this.source === "modicare") {
      this.VideoSection = "VideoDatas";
    } else {
      this.VideoSection = this.source + "VideoDatas";
    }
    let video_data =  await axios.get(this.hostApi+'/api/video/'+ this.$route.params.id + '/');
    this.title = video_data.data.title;
    this.video_link = video_data.data.video_link;
    this.embed_link = video_data.data.embed_link;
    this.section = video_data.data.section;
    this.status = video_data.data.status;
    this.thumbnail = video_data.data.thumbnail;
  },
  methods: {
    async UpdateVideoData(e){
        e.preventDefault();
        let {
        title,
        video_link,
        embed_link,
        section,
        status,
      } = this;
    try{
        const formData = {
          title,
          video_link,
          embed_link,
          section,
          status,
        };
        await editVideoData(this.hostApi, this.$route.params.id, formData);
        this.$toast.success(`Section added successfully!`, {
            position: "top",
            canTimeout: true,
            duration: 2500,
          });
      this.$router.push({name: this.VideoSection })
    }
    catch(e) {
        this.$toast.error(`${e.response.data.detail || e.message}`, {
          position: "top",
          canTimeout: true,
          duration: 2500,
        });
      }
    },
    cancelForm(){
        this.$router.push({ name: this.VideoSection })
    }
  }
}
</script>