<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">

          <form role="form">
            <div class="form-group input-group input-group-alternative mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <i class="ni ni-mobile-button"></i>
                </span>
              </div>
            <input type="text" placeholder="Mob Number" v-model="model.mob" class="form-control" aria-describedby="addon-right addon-left">
            </div>

             <div class="form-group input-group input-group-alternative mb-3">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <i class="ni ni-lock-circle-open"></i>
                </span>
              </div>
            <input type="password" placeholder="Password" v-model="model.password" class="form-control" aria-describedby="addon-right addon-left">
            </div>
            <base-checkbox class="custom-control-alternative">
              <span class="text-muted">Remember me</span>
            </base-checkbox>
            <div class="text-center">
              <button type="button" v-on:click="adminLogin()" class="btn btn-primary my-4">Sign in</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "login",
  data() {
    return {
      User:"",
      mob:'',
      model: {
        mob: "",
        password:"",
      },
    };
  },
  mounted() {
    if(localStorage.getItem("authentication_token")){
      this.$router.push('/dashboard');
    }
  },
  methods:{
    async adminLogin(){
      console.log(this.model)
      if(!this.model.mob || !this.model.password){
        this.$toast.error(`login failed, please check the creds`,{
            position: "top",
            canTimeout:true,
            duration:2500,
        });
        return;
      }
      var data = JSON.stringify({
      "mob": `+91${this.model.mob}`,
      "password": this.model.password
      });

      var config = {
        method: 'post',
        url: process.env.VUE_APP_SERVER_HOST+'/api/admin-login',
        headers: { 
          'Content-Type': 'application/json', 
        },
        data : data
      };
      try{
        let res = await axios(config)
        if(res.status == 200) {
          console.log(res.data)
          localStorage.setItem("authentication_token",res.data.token);
          localStorage.setItem("user_id",res.data.user_id);
          this.$store.dispatch('getUserProfile', res.data);
          this.$router.push('/content');
        }else{
          this.$toast.error(`Something went wrong`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
      }catch(e){
        this.$toast.error(`${e.message}`,{
          position: "top",
          canTimeout:true,
          duration:2500,
        })
      }
    }
  }
};
</script>
<style></style>
