<template>
  <form @submit.prevent="addUserBV"> 
    <div class="card-body">
        <div class="">
            <div class="row">
            <div class="col-6">
                <div class="form-group has-label">
                    <label class="form-control-label">
                        User Coin BV
                    </label>
                    <input
                        type="number"
                        placeholder="BV Balance"
                        v-model="user_bv"
                        class="form-control form-control-alternative"
                        aria-describedby="addon-right addon-left"
                        disabled="disabled"
                    />
                </div>
            </div>
            <div class="col-6">
                <div class="form-group has-label">
                    <label class="form-control-label">
                        BV to Add
                    </label>
                    <input
                        type="number"
                        placeholder="User BV"
                        v-model="bv"
                        class="form-control form-control-alternative"
                        aria-describedby="addon-right addon-left"
                        required
                    />
                </div>
            </div>
            </div>
        </div>
    </div>

    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row">
            <div class="col-12 text-right">
            <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
            <button type="submit" class="btn btn-info">Add BV</button>
            </div>
        </div>
        </div>
    </div>
</form>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
var axios = require('axios');
export default {
    name: "User BV",
     props: {
      source: {
        type: String,
        default: "modicare"
      },
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      userBv: {
        type: Number,
      },
      userId:{
        type: String,
      }
    },
    data() {
        return {
            BackUser: "",
            bv: 0,
            user_bv: 0
        }
    },
    mounted(){
        setInterval(() =>{
            this.user_bv = this.userBv;
        },1000)
    },
    methods: {
        async addUserBV(){
           console.log("hiiiiiiiiiiiiiiiii")
            if(this.bv == 0) return;
            var data = JSON.stringify({
                "requested_bv": parseInt(this.bv)
            });

            var config = {
            method: 'post',
            url: this.hostApi+'/api/userbv/'+ this.userId +'/?transaction_type=manual',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : data
            };

            const res = await axios(config);
            if(res.status === 200){
                this.user_bv = this.user_bv + parseInt(this.bv);
                this.$toast.success(`${res.data.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                this.resetForm()
            }else{
                this.$toast.error(`Something went wrong`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        },
        resetForm(){
            this.bv = 0;
        }
    }
}
</script>