<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            AdSense
          </h3>
        </div>
         <div class="col text-right">
          <base-button type="info" size="sm" @click="addSection()">Add AdSense</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading===true" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Ads Type</th>
          <th>Unit ID</th>
          <th>App ID</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                    <router-link :to="{name:ViewAdSense, params:{id:row.item.id}}" >{{row.item.ad_type}}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
               {{ row.item.unit_id }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
               {{ row.item.app_id }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
              <base-switch :value="row.item.status" v-on:change="changeStatus($event, row.item.id)"/>
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewAdSense, params:{id:row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="delAdSense(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>
  </div>
</template>
<script>
import { deleteAdsense, editAdSenseStatus, getAdsense } from "../../services/apiHandler";
import CustomLoader from "../../components/CustomLoader.vue";
export default {
    name: 'AdsenseTable',
    components:{
      CustomLoader
    },
    props: {
      type: {
        type: String,
      },
      title: String,
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            tableData:[],
            perpage:8,
            currentpage:1,
            pagination: {
                default: 1
            },
            ViewAdSense:"",
            ViewAddAdSense:"",
            loading: false,
        }
    },
    async mounted() {
        if(this.source === "modicare"){
          this.ViewAdSense = "AdSense";
          this.ViewAddAdSense = "AddAdSense"
        }
        else{
          this.ViewAdSense = this.source+"AdSense";
          this.ViewAddAdSense = this.source+"AddAdSense"
        }
        this.loading = true;
        let sections = await getAdsense(this.hostApi);
        this.tableData = sections.data;
        this.loading = false;
    },
    methods: {
      addSection(){
        this.$router.push({name: this.ViewAddAdSense})
      },
      async delAdSense(ad_id){
           this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) =>{
            if(result.isConfirmed) {
              deleteAdsense(this.hostApi, ad_id).then(() =>{
                  this.$swal('Deleted!','Selected Ads has been deleted.','success');
                  this.tableData = this.tableData.filter((obj)=>{return obj.id !== ad_id;})
              }).catch((e) =>{
                  this.$swal('Something went wrong!',e.message,'danger');
              })
            } 
        })
      },
      changeStatus(e, ad_id){
        editAdSenseStatus(this.hostApi, ad_id, e.target.checked).then(() =>{
          this.$swal('Updated!','Selected Ads status altered','success');
        }).catch((e) =>{
            this.$swal('Something went wrong!',e.response.data.message || e.response.data.detail,'danger');
        })
      }
    }
}
</script>