<template>
    <div class="add-brand">
    <base-header type="gradient" class="pt-5"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-12">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add New Tags</h3>
                  </div>
                </div>
              </div>
            </div>
          <form @submit.prevent="addTags()"> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             TagName
                          </label>
                          <input
                              type="text"
                              placeholder="Category Name"
                              v-model="tag"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                              required
                          />
                       </div>
                    </div>
                    <div class="col-6">
                        <div class="form-group has-label">
                            <label class="form-control-label">
                             TagImage
                          </label>
                          <div class="card">
                            <div v-if="tagImage">
                              <img class="card-img-top" :src="tagImage" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file" type="file" v-on:change="handleImage(1)" accept="image/*"/>
                              <button type="button" v-if="!tagImage" class="btn btn-primary btn-sm" @click="$refs.file.click()">Add</button>
                              <button type="button" v-else class="btn btn-primary btn-sm" @click="$refs.file.click()">Change</button>
                              <button type="button" :disabled="!tagImage" class="btn btn-info btn-sm" @click="removeImage(0)">Remove</button>
                            </div>
                          </div>
                        </div> 
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="submit" class="btn btn-info">Add TagName</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
      max-height: 170px;
      min-height: 170px;
  }
</style>
<script>
import axios from "axios";
var axiosDefaults = require("axios/lib/defaults");
axiosDefaults.xsrfCookieName = "csrftoken"
axiosDefaults.xsrfHeaderName = "X-CSRFToken"
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default({
    name:"Add Brand",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      }
    },
    data() {
       return{
           click:0,
           tag:'',
           tagImage:''
       } 
    },
    methods:{
        async addTags(){
            if(!this.tag)return;
            var data = JSON.stringify({
                "tagName": this.tag,
            });
            var config = {
            method: 'post',
            url: this.hostApi+'/api/tags/',
            headers: { 
                'Content-Type': 'application/json',
            },
            data : data,
            xsrfHeaderName: "X-CSRFToken",
            };
            let res = await axios(config);
            let tagId = res.data.id;
            if(res.status == 201){
               await this.TagImageUpload(tagId);
               this.clearForm();
               this.$toast.success(`BrandName added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.BrandName = "";
            }else{
               this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
            }
        },
        async handleImage(){
            let imageFile = this.$refs.file.files[0];
            this.tagImage = URL.createObjectURL(imageFile)
        },
        async removeImage(){
            this.tagImage = "";
        },
        async TagImageUpload(t_id){
            var formData = new FormData();
            formData.append('tagName', this.tag);
            formData.append('tagImage', this.$refs.file.files[0]);
            var config = {
                method: 'put',
                url: this.hostApi+'/api/tags/'+ t_id +'/',
                headers: { 
                   'Content-Type': 'multipart/form-data'
                },
                data:formData,
                xsrfHeaderName: "X-CSRFToken",
            }
            let res = await axios(config);
            if(res.status == 200){
                this.$toast.success(`new Image added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            }else{
                this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            }

        },
        clearForm(){
            this.tag = "",
            this.tagImage = "";
        }
    }
})
</script>
