<template>
    <div class="mx-auto container" v-html="terms_and_condition">
    </div>
</template>
<style scoped>
</style>
<script>
import axios from "axios";

export default {
    name: 'Terms And Conditions',
    data () {
        return {
            terms_and_condition:''
        }
    },
    async mounted() {
        let staticData = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/staticdata/");
        this.terms_and_condition = staticData.data[0].terms_and_condition;
    }
}
</script>