<template>
    <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            BV Requests
          </h3>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>MCA Number</th>
          <th>User Mob</th>
          <th>Requested BV</th>
          <th>User Mail</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.mca_number.mca_number }}</span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item?.mca_number?.user?.mob }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.requested_bv }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item?.mca_number?.user?.email }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status=='pending'" class="align-center text-warning">
                {{ row.item.status }}
            </div>
            <div v-else-if="row.item.status=='rejected'" class="align-center text-danger">
                {{ row.item.status }}
            </div>
            <div v-else class="align-center text-success">
                {{ row.item.status }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status=='pending'">
                <button class="btn btn-sm btn-success" v-on:click="ChangeStatus(row.item.id,'paid')">Pay</button>
                <button class="btn btn-sm btn-danger" v-on:click="ChangeStatus(row.item.id,'rejected')">Reject</button>
            </div>
            <div v-else>
                <button class="btn btn-sm btn-secondary" disabled>No Action</button>
            </div>
          </td>

        </template>
      </base-table>
    </div>
    <TablePagination :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import TablePagination from "../../components/Paginator/paginator.vue";
import { getBvRequestData, changeBvRequest, jumpToPage } from "../../services/apiHandler";
import CustomLoader from "../../components/CustomLoader.vue";

export default {
    name: "BVRequestsTable",
    components: {
      TablePagination,
      CustomLoader
    },
    props: {
        type: {
        type: String,
        },
        title: String,
        source: {
        type: String,
        default: "modicare"
        },
        hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
        }
  },
  data(){
    return {
        tableData:[],
        next: null,
        prev: null,
        all_pages:[],
        currentpage: 1,
        loading: false
    }
  },
  async mounted() {
    this.loading = true;
    let bv_lists = await getBvRequestData(this.hostApi);
    await this.MapData(bv_lists);
    this.loading = false;
  },
  methods: {
    async ChangeStatus(id, status){
      changeBvRequest(this.hostApi, id, status).then((response) =>{
        this.tableData = this.tableData.map(p =>p.id === id
        ? { ...p, status: response.data.status }
        : p
        );
          this.$toast.success(`BV Requested Successfully ${status}`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }).catch((e) =>{
            this.$toast.error(`${e.response.data.detail}`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
          }
        )
    },
    async getNextPrevPageData(url){
      this.loading = true;
      const result = await jumpToPage(url, true)
      await this.MapData(result);
      this.loading = false;
    },
    async MapData(result){
      this.tableData = result.data.results;
      this.next = result.data.next;
      this.prev = result.data.previous; 
      this.currentpage = result.data.current_page;
      this.all_pages = result.data.all_pages;
    }
  }
}
</script>