<template>
    <div class="card shadow bg-secondary">
    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row align-items-center">
            <div class="col">
            <h3 class="mb-0">Dynamic Timers</h3>
            </div>
        </div>
        </div>
    </div>
       <div class="card-body">
               <div class="">
                 <div class="row">
                    <div class="col-6">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          After Click on Watch BTN - Timer
                        </label>
                        <div class="row">
                            <div class="col-4">
                                <select class="form-control" v-model="post_click_opt" v-on:change="TimeTogglerPostClick($event, formData.post_click_wvdo_btn)">
                                    <option value="min">Minutes</option>
                                    <option value="sec">Seconds</option>
                                </select>
                            </div>
                            <div class="col-8">
                                <input type="number" class="form-control" placeholder="Max BV per day" name="post_click_wvdo_btn" v-model="formData.post_click_wvdo_btn">
                            </div>
                        </div>
                        
                      </div>
                    </div>
                    <div class="col-6">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          Default -  Timer
                        </label>
                        <div class="row">
                            <div class="col-4">
                                <select class="form-control" v-model="pre_click_opt" v-on:change="TimeTogglerPreClick($event, formData.pre_click_wvdo_btn)">
                                    <option value="min">Minutes</option>
                                    <option value="sec">Seconds</option>
                                </select>
                            </div>
                            <div class="col-8">
                                <input type="number" class="form-control" placeholder="Mininmum Products" name="pre_click_wvdo_btn" v-model="formData.pre_click_wvdo_btn">
                            </div>
                        </div>
                      </div>
                    </div>
                 </div>
               </div>
             </div>
        <div class="card-footer">
        <div class="bg-white border-0">
        <div class="row">
            <div class="col-12 text-right">
            <button type="button" class="btn btn-info" v-on:click="DynamicTimerUpdate()">Update</button>
            <!-- <button type="button">Add</button> -->
            </div>
        </div>
        </div>
    </div>
    </div>
</template>
<script>
import { getDynamicTimer, updateDynamicTimer } from "../../services/apiHandler";

export default {
    name: "TimerView",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
    },
    data() {
        return {
            formData : {
                post_click_wvdo_btn: 0,
                pre_click_wvdo_btn: 0,
                id: null
            },
            post_click_opt: "min",
            pre_click_opt: "min"
        }
    },
    async mounted() {
        const response = await getDynamicTimer(this.hostApi);
        if(response.data.length > 0){
            this.formData = response.data[0];
        }
    },
    methods: {
        async DynamicTimerUpdate(){
            try {
                if(!this.formData.id)return alert("Dynamic Timer Id is required");
                const formData_ = await this.updateTimerValueAsMIn()
                await updateDynamicTimer(this.hostApi, formData_);
                this.$toast.success(`Timer Data updated successfully`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            } catch (e) {
                this.$toast.error(`${e.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        },

        async TimeTogglerPostClick(e, value){
            const res = await this.TimeSync(e.target.value, value);
            this.formData.post_click_wvdo_btn = res;
        },

        async TimeTogglerPreClick(e, value){
            const res = await this.TimeSync(e.target.value, value);
            this.formData.pre_click_wvdo_btn = res;
        },
        async TimeSync(time, value){
            switch(time){
                case 'min':
                    return (1/60*parseFloat(value)).toFixed(2)
                case 'sec':
                    return (parseFloat(value) * 60).toFixed(2)
                default:
                    return value
            }
        },

        async updateTimerValueAsMIn(){
            const checkFormData = JSON.parse(JSON.stringify(this.formData));
            if(this.post_click_opt !== 'min'){
                checkFormData.post_click_wvdo_btn = await this.TimeSync("min", this.formData.post_click_wvdo_btn);
            }
            if(this.pre_click_opt !== 'min'){
                checkFormData.pre_click_wvdo_btn = await this.TimeSync("min", this.formData.pre_click_wvdo_btn);
            }
            return checkFormData;
        }
    }
}
</script>