import { createStore } from 'vuex'
import axios from 'axios';

const store = createStore({
    state: {
        title: 'Vuex Store',
        section_datas: {
            'Why to use':'Why to use',
            'Demo Videos':'Demo Videos',
            'Business Opportunity':'Business Opportunity',
            'Training Videos':'Training Videos',
            'Motivational Videos':'Motivational Videos',
            'Success Stories':'Success Stories',
            'Testimonial Videos':'Testimonial Videos',
            'About Modicare':'About Modicare',
            'About Vestige':'About Vestige',
            'About HHI':'About HHI',
            'About ProteinWorld':'About Protein World',
            'About AmulyaHerbal':'About Amulya Herbal',
            'Become a Partner':'Become a Partner',
            'Previous Events':'Previous Events',
            'Upcoming Events':'Upcoming Events',
            'Book Ticket':'Book Ticket',
            'Broucher':'Broucher'
        },
        section_types: {
            'video': 'Video',
            'static': 'Static'
        },
        section_names: {
            'Learnings': 'Learnings',
            'Archivers': 'Archivers',
            'Events': 'Events',
        },
        modicare_section: {
            'Why Modicare':'Why Modicare',
        },
        hhi_section: {
            'Why HHI':'Why HHI',
        },
        vestige_section: {
            'Why Vestige':'Why Vestige',
        },
        proteinworld_section: {
            'Why ProteinWorld':'Why ProteinWorld',
        },
        amulyaherbal_section: {
            'Why AmulyaHerbal':'Why AmulyaHerbal',
        },
        dp_types: {
            'Modicare Super Store':'Modicare Super Store',
            'Modicare Success Center (MSC)':'Modicare Success Center (MSC)',
            'Modicare Lifestyle Center':'Modicare Lifestyle Center',
            'Modicare Access Point (TFS Store)':'Modicare Access Point (TFS Store)',
            'DISTRIBUTION POINTS (DPS)':'DISTRIBUTION POINTS (DPS)'
        },
        hhi_dp_types: {
            'hhi Super Store':'hhi Super Store',
            'hhi Success Center (MSC)':'hhi Success Center (MSC)',
            'hhi Lifestyle Center':'hhi Lifestyle Center',
            'hhi Access Point (TFS Store)':'hhi Access Point (TFS Store)',
            'DISTRIBUTION POINTS (DPS)':'DISTRIBUTION POINTS (DPS)'
        },
        vestige_dp_types: {
            'Vestige Super Store':'Vestige Super Store',
            'Vestige Success Center (MSC)':'Vestige Success Center (MSC)',
            'Vestige Lifestyle Center':'Vestige Lifestyle Center',
            'Vestige Access Point (TFS Store)':'Vestige Access Point (TFS Store)',
            'DISTRIBUTION POINTS (DPS)':'DISTRIBUTION POINTS (DPS)'
        },
        amulya_herbal_dp_types: {
            'AmulyaHerbal Super Store':'AmulyaHerbal Super Store',
            'AmulyaHerbal Success Center (MSC)':'AmulyaHerbal Success Center (MSC)',
            'AmulyaHerbal Lifestyle Center':'AmulyaHerbal Lifestyle Center',
            'AmulyaHerbal Access Point (TFS Store)':'AmulyaHerbal Access Point (TFS Store)',
            'DISTRIBUTION POINTS (DPS)':'DISTRIBUTION POINTS (DPS)'
        },
        protein_world_dp_types: {
            'ProteinWorld Super Store':'ProteinWorld Super Store',
            'ProteinWorld Success Center (MSC)':'ProteinWorld Success Center (MSC)',
            'ProteinWorld Lifestyle Center':'ProteinWorld Lifestyle Center',
            'ProteinWorld Access Point (TFS Store)':'ProteinWorld Access Point (TFS Store)',
            'DISTRIBUTION POINTS (DPS)':'DISTRIBUTION POINTS (DPS)'
        },
        authenticated: false,
        static_data:{},
        user_data:{},
    },
    getters:{
        static_data: (state) => state.static_data,
        user_data: (state) => state.user_data
    },
    mutations: {
        setAuthentication(state, status){
            state.authenticated = status;
        }, 
        SET_STATIC_DATA(state, data){
            state.static_data = data;
        },
        UPDATE_ABOUT_US(state, payload){
            let [key, value] = Object.entries(payload)[0];            
            state.static_data[key] = value;
        },
        USER_DATA(state, payload){
            state.user_data = payload;
        }

    },
    actions: {
        getStaticData({commit},{url}){
            axios.get(url+`/api/staticdata/`)
            .then(response => {
                console.log("store",response.data)
                commit('SET_STATIC_DATA', response.data[0])
            })
        },
        addStaticData({commit}, payload){
            commit('UPDATE_ABOUT_US',payload)
        },
        getUserProfile({commit}, payload){
            axios.get(process.env.VUE_APP_SERVER_HOST+`/api/user/${payload.user_id}/`)
            .then(response => {
                console.log(response)
                commit("USER_DATA", response.data)
                commit("setAuthentication", true);
            })
        }
    }
})

export default store;