<template>
    <div class="mx-auto container" v-html="about_us">
    </div>
</template>
<style scoped>
</style>
<script>
import axios from "axios";

export default {
    name: 'About Us',
    data () {
        return {
            about_us:''
        }
    },
    async mounted() {
        let staticData = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/staticdata/");
        this.about_us = staticData.data[0].about_us;
    }
}
</script>