<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Order Products
          </h3>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Product</th>
          <th>Price/ Qty</th>
          <th>QTY ORDERED</th>
          <th>Business Volume</th>
          <th>Personal Volume</th>
          <th>Amount</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                    {{ row.item.product.productCode }}
                     <br>
                     {{ row.item.product.brand.BrandName }} - {{ row.item.product.productName }}
                </span>
              </div>
          </th>
           <td class="text-align-center">
            <div class="align-center">
                <b>Rs. {{ parseFloat(row.item.product.productPrice).toFixed(2) }} /-</b>
            </div>
            <div class="">
                {{ row.item.product.quantity }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.productQty }}
            </div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.businessVolume }}
            </div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ parseFloat(row.item.product.personalVolume).toFixed(2) }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <b> Rs. {{ (parseFloat(row.item.product.productPrice)* row.item.productQty).toFixed(2) }} /-</b> 
            </div>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
    name: 'orderproducts table',
    props: {
        type: {
            type: String,
        },
        title: String,
        hostApi: {
          type: String,
          default: process.env.VUE_APP_SERVER_HOST
        },
        source: {
          type: String,
          default: "modicare"
        }
    },
    data(){
        return {
            tableData: [],
        }
    },
    async mounted(){
        let order_id = this.$route.params.id;
        let res = await axios.get(this.hostApi+`/api/orderProduct/?order=${order_id}`);
        this.tableData = res.data;
    }
}
</script>
