<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Brands
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Brand With Brand ID, Brand Name"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addBrand()" size="sm">Add Brand</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading===true" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Brand ID</th>
          <th>Brand Name</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <router-link :to="{name: editbrand, params:{id:row.item.id}}" >{{ row.item.id }}</router-link>
              </div>
          </th>
          <td class="text-align-center">
            <div class="media-body">
                <span class="name mb-0 text-sm">{{ row.item.BrandName }}</span>
              </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name: editbrand, params:{id:row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="delBrand(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>

        </template>
      </base-table>
    </div>
    <TablePagination :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import { deleteBrand, getBrands, searchingBrand, jumpToPage } from "../../services/apiHandler";
import TablePagination from "../../components/Paginator/paginator.vue";
import CustomLoader from "../../components/CustomLoader.vue";
export default {
  name: "brands-table",
  components:{
    TablePagination,
    CustomLoader,
  },
  props: {
    type: {
      type: String,
    },
    title: String,
    source: {
      type: String,
      default: "modicare"
    },
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    }
  },
  data() {
    return {
      tableData: [],
      next: null,
      prev: null,
      all_pages:[],
      perpage:8,
      currentpage:1,
       pagination: {
        default: 1
      },
      editbrand:"EditBrand",
      searchQuery:"",
      loading: false
    };
  },
  async mounted() {
      if(this.source !== "modicare"){
        this.editbrand = this.source+"EditBrand";
      }
      this.loading = true;
      let brands = await getBrands(this.hostApi);
      await this.MapData(brands)
      this.loading = false;
  },
  methods:{
    addBrand(){
        if(this.source === "modicare"){
          this.$router.push('/addbrand');
        }
        else{
          this.$router.push(`/${this.source.toLowerCase()}/addbrand`);
        }
    },
    async getNextPrevPageData(url){
      const result = await jumpToPage(url)
      await this.MapData(result);
    },
    async MapData(result){
      this.tableData = result.data.results;
      this.next = result.data.next;
      this.prev = result.data.previous; 
      this.currentpage = result.data.current_page;
      this.all_pages = result.data.all_pages;
    },
    async searchTable(){
      const response = await searchingBrand(this.hostApi, this.searchQuery);
        if(response.data.length == 0){
          this.$toast.error(`No Results Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
        await this.MapData(response);
    },
    async delBrand(brand_id){
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) =>{
        if(result.isConfirmed) {
          deleteBrand(this.hostApi, brand_id).then(() =>{
              this.$swal('Deleted!','Selected BrandName has been deleted.','success');
              this.tableData = this.tableData.filter((obj)=>{return obj.id !== brand_id;})
            }).catch((e) =>{
              console.log(e)
              this.$swal('Something went wrong!',e.response.data.detail,'danger');
            })
        } 
      })
    }
  }
};
</script>
