<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ProductPage }" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">View/Upadate Product</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form> 
                <h6 class="heading-small text-muted mb-4">Product information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             Brand
                          </label>
                         <select class="form-control form-control-alternative" v-model="BrandID">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in brand_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.BrandName }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Category
                            </label>
                            <select class="form-control form-control-alternative" v-model="selectedCategory">
                               <option disabled value="">Please select one</option>
                              <option
                                v-for="item in tag_hirarchy"
                                :key="item.category.id"
                                :value="item.category.id"
                              >
                                {{ item.category.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Subcategory
                            </label>
                            <select class="form-control form-control-alternative" :disabled="selectedCategory === ''" v-model="selectedSubCategory">
                              <option disabled value="">Please select one</option>
                              <option
                                v-for="item in subCatList[selectedCategory]"
                                :key="item.id"
                                :value="item.id"
                              >
                                {{ item.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Code
                            </label>
                            <input
                              placeholder="Product Code"
                              id="productCode"
                              v-model="ProductCode"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Name
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="ProductName"
                              v-model="ProductName"
                              placeholder="Product Name"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                              Quantity
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="Product Quantity"
                              v-model="Quantity"
                              placeholder="Product Quantity"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Pricing Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Price
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="ProductPrice"
                              placeholder="Price Per Piece"
                              type="text"
                            />
                         </div>
                      </div>
                       <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Discounted Price
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="DiscountedPrice"
                              placeholder="Price Per Piece"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               BusinessVolume
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="bv"
                              v-model="BusinessVolume"
                              placeholder="Business Volume"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               PersonalVolume
                            </label>
                            <input
                              placeholder="Average Volume"
                              id="av"
                              v-model="PersonalVolume"
                              readonly="readonly"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>


                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Description</h6>
                <div class="">
                  <div class="row">

                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="productDescription">
                              Product Description
                           </label>
                          <textarea rows="4" type="text" placeholder="ProductDescription" id="productDescription" v-model="ProductDesc" class="form-control form-control-alternative"></textarea>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="colorDescription">
                              Color Description
                           </label>
                          <textarea type="text"  rows="4" placeholder="Color Description" id="colorDescription" v-model="ColorDesc" class="form-control form-control-alternative" ></textarea>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-12">
                      <div class="form-group">
                        <div class="card-deck">
                          <div class="card">
                            <div v-if="image_urls[0]">
                              <img class="card-img-top" :src="image_urls[0]" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file1" type="file" v-on:change="handleImageUpload(1)" accept="image/*"/>
                              <button v-if="!image_urls[0]" class="btn btn-primary btn-sm" type="button" @click="$refs.file1.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" type="button" @click="$refs.file1.click()">Change</button>
                              <button :disabled="!image_urls[0]" class="btn btn-info btn-sm" type="button" @click="removeImage(0)">Remove</button>
                            </div>
                          </div>
                          <div class="card">
                            <div v-if="image_urls[1]">
                              <img class="card-img-top" :src="image_urls[1]" alt="image ..2"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file2" type="file" v-on:change="handleImageUpload(2)" accept="image/*"/>
                              <button v-if="!image_urls[1]" class="btn btn-primary btn-sm" type="button" @click="$refs.file2.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" type="button" @click="$refs.file2.click()">Change</button>
                              <button :disabled="!image_urls[1]" class="btn btn-info btn-sm" type="button" @click="removeImage(1)">Remove</button>
                            </div>
                          </div>
                          <div class="card">
                            <div v-if="image_urls[2]">
                              <img class="card-img-top" :src="image_urls[2]" alt="image ..3"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file3" type="file" v-on:change="handleImageUpload(3)" accept="image/*"/>
                              <button v-if="!image_urls[2]" class="btn btn-primary btn-sm" type="button" @click="$refs.file3.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" type="button" @click="$refs.file3.click()">Change</button>
                              <button :disabled="!image_urls[2]" class="btn btn-info btn-sm" type="button" @click="removeImage(2)">Remove</button>
                            </div>
                          </div>
                          <div class="card">
                            <div v-if="image_urls[3]">
                              <img class="card-img-top" :src="image_urls[3]" alt="image ..4"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file4" type="file" v-on:change="handleImageUpload(4)" accept="image/*"/>
                              <button v-if="!image_urls[3]" class="btn btn-primary btn-sm" type="button" @click="$refs.file4.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" type="button" @click="$refs.file4.click()">Change</button>
                              <button :disabled="!image_urls[3]" class="btn btn-info btn-sm" type="button" @click="removeImage(3)">Remove</button>
                            </div>
                          </div>
                          <div class="card">
                            <div v-if="image_urls[4]">
                              <img class="card-img-top" :src="image_urls[4]" alt="image ..5"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file5" type="file" v-on:change="handleImageUpload(5)" accept="image/*"/>
                              <button v-if="!image_urls[4]" class="btn btn-primary btn-sm"  type="button" @click="$refs.file5.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm"  type="button" @click="$refs.file.click()">Change</button>
                              <button :disabled="!image_urls[4]" class="btn btn-info btn-sm" type="button" @click="removeImage(4)">Remove</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Loyalty
                           </label>
                            <select class="form-control form-control-alternative" v-model="Loyalty">
                              <option value="true">Yes</option>
                              <option value="false">No</option>
                            </select>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <router-link type="primary" :to="{name:'Products'}" class="btn btn-secondary mr-2"
                      >Cancel</router-link>
                    <button
                      type="primary"
                      class="btn btn-info"
                      v-on:click="updateProduct()"
                      >Update Product</button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select, textarea{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
    max-height: 172px;;
  }
  .card-img-top{
    min-height: 172px;;
  }
</style>
<script>
import axios from "axios";
import { addProductImage, deleteProductImage, editProduct, editProductImage, getAllBrands } from "../../services/apiHandler";

export default({
    name: "View/Edit Product",
    props:{
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default: "modicare"
      },
    },
    data(){
        return {
            brand_lists:[],
            image_urls:[],
            BrandID:"",
            ProductCode:"",
            ProductName:"",
            ProductPrice:"",
            DiscountedPrice: "",
            GST:"",
            Discount:"",
            Availability:"",
            Quantity: "",
            ProductDesc:"",
            ColorDesc:"",
            BusinessVolume:"",
            PersonalVolume:"",
            tag_hirarchy:[],
            subCatList:{},
            selectedCategory:'',
            selectedSubCategory:'',
            Status:true,
            Loyalty: false,
            productImageIds:[],
            ProductPage:""
        }
    },
    async mounted() {
        if(this.source === "modicare"){this.ProductPage = "Products"}
        else{this.ProductPage = this.source+"Products"}
        let product_id = this.$route.params.id;
        let products = await axios.get(this.hostApi+`/api/products/${product_id}/`);
        this.BrandID = products.data.brand.id;
        this.ProductCode = products.data.productCode;
        this.ProductName = products.data.productName;
        this.ProductPrice = products.data.productPrice;
        this.DiscountedPrice = products.data.discount;
        this.ProductDesc = products.data.ProductDescription;
        this.ColorDesc = products.data.colorDesc;
        this.BusinessVolume = products.data.businessVolume || 0;
        this.PersonalVolume = products.data.personalVolume || 0;
        this.Discount = products.data.discount;
        this.Availability = products.data.availability;
        this.Quantity = products.data.quantity;
        this.Status = products.data.status;
        this.Loyalty = products.data.loyalty
        let brands = await getAllBrands(this.hostApi);
        this.brand_lists = brands.data.data;
        this.listTagHirarchy();
        this.getTagPivot();
        this.getImages();
    },
    methods: {
       async listTagHirarchy(){
           let listTags = await axios.get(this.hostApi+`/api/tagHirarchy/`);
           this.tag_hirarchy = listTags.data;
           this.tag_hirarchy.forEach((data)=>{
             this.subCatList[data.category.id] = data.subcategory;
           })
       },
       async getTagPivot(){
           let product_id = this.$route.params.id;
           let tagPivots = await axios.get(this.hostApi+`/api/main-tagpivot/?product=${product_id}`);
           console.log(tagPivots)
           this.selectedCategory = tagPivots.data.results[0].category.id;
           this.selectedSubCategory = tagPivots.data.results[0].subcategory.id;
       },
       async getImages(){
        let product_id = this.$route.params.id;
        let images = await axios.get(this.hostApi+`/api/${product_id}/images/`);
        images.data.forEach((data)=>{
          if(data.product == product_id){
            // if(data.image_url){
            //   this.image_urls.push(data.image_url)  
            // } 
            if(data.image){
              this.image_urls.push(data.image) ;
              this.productImageIds.push(data.id);
            }
          }
        })
       },
       async updateProduct(){
        try{
          await this.updateImageData()
         let product_id = this.$route.params.id;
         const {BrandID, ProductCode, ProductName, ProductPrice, selectedCategory, selectedSubCategory, DiscountedPrice, Availability, Status, BusinessVolume, ProductDesc,ColorDesc, Quantity, Loyalty} = this;
         var data = JSON.stringify({
            "productCode": ProductCode,
            "productName": ProductName,
            "productPrice": ProductPrice,
            "brand": BrandID,
            "status": Status,
            "availability": Availability,
            "discount": DiscountedPrice,
            "ProductDescription": ProductDesc,
            "colorDesc": ColorDesc,
            "businessVolume": BusinessVolume,
            "quantity":Quantity,
            "category":selectedCategory,
            "subcategory":selectedSubCategory,
            "loyalty":Loyalty
          });
          await editProduct(this.hostApi, product_id, data);
          this.$toast.success(`Product updated successfully!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }catch(e) {
          this.$toast.success(`${e.response.data.detail}`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
         
       },
       async handleImageUpload(ind){
         let file_ = "file"+ind
         let uploadedImage = this.$refs[file_].files;
         this.image_urls[ind-1] = URL.createObjectURL(uploadedImage[0]);
       },
       async removeImage(ind){
        try {
          if(this.productImageIds[ind]){
           await deleteProductImage(this.hostApi, this.productImageIds[ind]);
            this.$toast.success(`Image removed successfully!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
            this.productImageIds[ind] = undefined;
         }
        } catch (e) {
          this.$toast.error(`${e.response.data.detail}`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
       },
       async updateImageData(){
        if(this.$refs.file1.files.length > 0){
          if(this.productImageIds[0]){
            this.updateImage(1, this.productImageIds[0])
          }else{
            this.addImage(1)
          }
        }
        if(this.$refs.file2.files.length > 0){
          if(this.productImageIds[1]){
            this.updateImage(2, this.productImageIds[1])
          }else{
            this.addImage(2)
          }
        }
        if(this.$refs.file3.files.length > 0){
          if(this.productImageIds[2]){
            this.updateImage(3, this.productImageIds[2])
          }else{
            this.addImage(3)
          }
        }
        if(this.$refs.file4.files.length > 0){
          if(this.productImageIds[3]){
            this.updateImage(4, this.productImageIds[3])
          }else{
            this.addImage(4)
          }
        }
        if(this.$refs.file5.files.length > 0){
          if(this.productImageIds[4]){
            this.updateImage(5, this.productImageIds[4])
          }else{
            this.addImage(5)
          }
        }
      },
      async addImage(ind){
        try{
          let product_id = this.$route.params.id;
          let file_ = 'file'+ind          
          await addProductImage(this.hostApi, product_id, this.$refs[file_].files[0]);
          this.$toast.success(`new Image added successfully!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
        }catch(e){
          this.$toast.error(`${e.response.data.detail}`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
      },
      async updateImage(ind, pm_id){
        try{
            let product_id = this.$route.params.id;
            let file_ = 'file'+ind
            await editProductImage(this.hostApi, pm_id, product_id, this.$refs[file_].files[0]);
            this.$toast.success(`Image updated successfully!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }catch(e) {
            this.$toast.error(`${e.response.data.detail}`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
        }
    }
})
</script>