<template>
    <div class="spinner-container" v-if="isLoading">
      <div class="spinner"></div>
    </div>
  </template>
  
  <script>
  export default {
    name:"CustomLoader",
    props:{
        isLoading:{
            type: Boolean,
            default: false
        }
    },
  };
  </script>
  
  <style>
  .spinner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  
  .spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: #36D7B7;
    animation: spin 1s linear infinite;
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }
  </style>
  