<template>
    <div class="add-brand">
    <div class="container-fluid mt--7 pt-4">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-12">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add New Categories</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row pb-3">
                    <div class="col-2">
                      <button type="button" class="btn btn-info" @click="init()"><i class="ni ni-button-power"></i> Refresh</button>
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Category
                          </label>
                         <select class="form-control form-control-alternative" v-model="CategoryTag">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in tag_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.tagName }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             SubCatgory
                          </label>
                            <select multiple class="form-control form-control-alternative" name="sub_cats[]" v-model="SubCatgory">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in tag_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.tagName }}
                            </option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="addTagHirarchy()">Add Category</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";

var axiosDefaults = require("axios/lib/defaults");
axiosDefaults.xsrfCookieName = "csrftoken"
axiosDefaults.xsrfHeaderName = "X-CSRFToken"
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default({
    name:"Add Tag Hirarchy",
    props:{
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      }
    },
    data() {
       return{
           click:0,
           Category:'',
           CategoryTag:'', 
           category_tags:'',
           SubCatgory:[],
           tag_lists: [],
       } 
    },
    async mounted(){
     this.init();
    },
    methods:{
        async addTagHirarchy(){
          if(!this.CategoryTag)return;
           var data = JSON.stringify({
                "category": this.CategoryTag,
                "subcategory":this.SubCatgory
            });
            var config = {
              method: 'post',
              url: this.hostApi+'/api/add/tagHirarchies/',
              headers: { 
                  'Content-Type': 'application/json',
              },
              data : data,
              xsrfHeaderName: "X-CSRFToken",
            };
            let res = await axios(config);
            if(res.status == 201){
              this.$toast.success(`tag hirarchy added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.resetForm();
            }else{
              this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
            }
        },
        async init(){
          let allTags = await axios.get(this.hostApi+`/api/tags/`);
          let allCategories = await axios.get(this.hostApi+`/api/tagHirarchy/`);
          // const tags = new Set(allTags.data.map(item=>item.tagName))
          const cats = new Set(allCategories.data.map(item=>item.category.tagName))
          let res = [...allTags.data.filter(item=>!cats.has(item.tagName))]
          this.tag_lists = res;
        },
        resetForm(){
          this.CategoryTag = "";
          this.SubCatgory = [];
        }
    }
})
</script>
