<template>
  <div>
    <base-header type="gradient" class="pb-6 pb-8 pt-5 pt-md-8">
    </base-header>
    <div class="container-fluid mt--8">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12">
          <div class="card shadow" :class="type === 'bg-default'">
                <div
                class="card-header border-0"
                :class="type ==='bg-transparent'"
                >
                <div class="row align-items-center no-gutters">
                    <div class="col">
                    <!-- <h3 class="mb-0" :class="type === 'text-white'">
                        Static Content
                    </h3> -->
                    </div>
                </div>
                <div class="">
                   <ul class="nav nav-pills nav-justified">
                        <li class="nav-item">
                            <a class="nav-link" @click.prevent="setActive('home')" :class="{ active: isActive('home') }" href="#home">About US</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" @click.prevent="setActive('profile')" :class="{ active: isActive('profile') }" href="#profile">Terms and Conditions</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" @click.prevent="setActive('contact')" :class="{ active: isActive('contact') }" href="#contact">Privacy Policy</a>
                        </li>
                         <li class="nav-item">
                            <a class="nav-link" @click.prevent="setActive('collect-bv')" :class="{ active: isActive('collect-bv') }" href="#collect-bv">How To Collect BV</a>
                        </li>
                    </ul>
                    <div class="tab-content py-3" id="myTabContent">
                        <div class="tab-pane fade" :class="{ 'active show': isActive('home') }" id="home">
                            <div class="card border-primary">
                                <QuillEditor v-model:content="about_us" contentType="html"  theme="snow" toolbar="full" />
                                <button @click="updateAboutUs" class="btn btn-info">Update Content</button>
                            </div>
                        </div>
                        <div class="tab-pane fade" :class="{ 'active show': isActive('profile') }" id="profile">
                            <div class="card border-primary">
                                <QuillEditor v-model:content="terms_and_condition" contentType="html"  theme="snow" toolbar="full" />
                                <button @click="updateTermsAndConditions" class="btn btn-info">Update Content</button>
                            </div>
                        </div>
                        <div class="tab-pane fade" :class="{ 'active show': isActive('contact') }" id="contact">
                            <div class="card border-primary mt-2">
                                <QuillEditor v-model:content="privacy_policy" contentType="html"  theme="snow" toolbar="full" />
                                <button @click="updatePrivacyPolicy" class="btn btn-info">Update Content</button>
                            </div>
                        </div>
                         <div class="tab-pane fade" :class="{ 'active show': isActive('collect-bv') }" id="collect-bv">
                            <div class="card border-primary mt-2">
                                <QuillEditor v-model:content="how_to_collect_bv" contentType="html"  theme="snow" toolbar="full" />
                                <button @click="updateCollectBV" class="btn btn-info">Update Content</button>
                            </div>
                        </div>
                    </div>
                </div>
                </div>                
            </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { QuillEditor } from '@vueup/vue-quill'
import '@vueup/vue-quill/dist/vue-quill.snow.css';
import axios from "axios";

export default {
  name: "StaticContent",
  components: {
    QuillEditor,
  },
  props: {
    hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
        type: String,
        default:"modicare"
    }
  },
  data(){
        return{
            about_us:'',
            terms_and_condition:'',
            privacy_policy:'',
            how_to_collect_bv: '',
            activeItem: 'home',
        }
    },
    computed: {
        getStaticData(){
           return this.$store.getters.static_data;
        },
        staticData(){
            return this.$store.state.static_data;
        }
    },
    created(){
        let res = this.getStaticData;
        this.about_us = res.about_us;
        this.privacy_policy = res.privacy_policy;
        this.terms_and_condition = res.terms_and_condition;
        this.how_to_collect_bv = res.how_to_collect_bv;
    },
    mounted() {
        this.$store.dispatch('getStaticData', { url:this.hostApi }).then(()=>{
            console.log("res", this.getStaticData);
        });
    },
    methods: {
        isActive (menuItem) {
            return this.activeItem === menuItem
        },
        setActive (menuItem) {
            this.activeItem = menuItem
        },
        updateAboutUs(){
            this.$store.dispatch('addStaticData',
            {about_us: this.about_us}
            )
            this.updateData({about_us: this.about_us})
        },
        updatePrivacyPolicy(){
            this.$store.dispatch('addStaticData',{
                privacy_policy: this.privacy_policy
            })
            this.updateData({privacy_policy: this.privacy_policy})
        },
        updateTermsAndConditions(){
            this.$store.dispatch('addStaticData',{
                terms_and_condition: this.terms_and_condition
            })
            this.updateData({terms_and_condition: this.terms_and_condition})
        },
        updateCollectBV(){
            this.$store.dispatch('addStaticData',{
                how_to_collect_bv: this.how_to_collect_bv
            })
            this.updateData({how_to_collect_bv: this.how_to_collect_bv})
        },
        async updateData(payload){
            let [key, value] = Object.entries(payload)[0];            
            let data = {}
            data[key] = value;
            var config = {
                method: 'patch',
                url: this.hostApi+'/api/staticdata/'+this.$store.state.static_data.id+'/',
                headers: { 
                    'Content-Type': 'application/json'
                },
                data : JSON.stringify(data)
            };
            let result = await axios(config);
            if(result.status == 200){
                this.$toast.success(`${key} updated successfully`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }else{
                this.$toast.error(`${key} updated failed`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        }
    }
};
</script>
<style>
    .ql-editor {
        max-height: 400px;
        min-height: 400px;
    }
</style>
