<template>
    <div class="add-addsense">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewAdSense}"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add AddSense</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-4">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Ad Type
                          </label>
                         <input type="text" class="form-control" placeholder="Ads Type" name="ad_type" v-model="ad_type">
                       </div>
                    </div>
                    <div class="col-4">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Unit ID
                          </label>
                          <input type="text" class="form-control" placeholder="Unit ID" name="unit_id" v-model="unit_id">
                       </div>
                    </div>
                    <div class="col-4">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             App ID
                          </label>
                          <input type="text" class="form-control" placeholder="App ID" name="app_id" v-model="app_id">
                       </div>
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-footer">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="addingAdsense()">Add AdSense</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
    input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import { addAdsense } from "../../services/apiHandler";

export default {
    name: "Add AdSense",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            ad_type: "",
            unit_id: "",
            app_id: "",
            ViewAdSense:""
        }
    },
    mounted() {
      if(this.source === "modicare"){
          this.ViewAdSense = "AdSense";
        }
        else{
          this.ViewAdSense = this.source+"AdSense";
        }
    },
    methods: {
        async addingAdsense(){
            try{
              const { ad_type, unit_id, app_id } = this;
              const form_data = { ad_type, unit_id, app_id };
              await addAdsense(this.hostApi, form_data)
              this.$toast.success(`AdSense added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
              await this.resetForm()
            }catch(e) {
              console.log("err", e.response.data)
                this.$toast.error(`${e.response.data.detail || e.response.data.message}`,{
                  position: "top",
                  canTimeout:true,
                  duration:2500,
              });
            }
        },
        async resetForm(){
            this.ad_type = "";
            this.unit_id = "";
            this.app_id = "";
        }
    }
}
</script>