<template>
    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!prev"
            @click="moveToPage(prev)"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li v-if="loading===true" class="text-center">
          <span class="text-info">loading...</span>
        </li>
        <li v-else class="text-center" 
        style="max-width: 55px"
        v-for="item in requirePages"
        :key="item"
        >
          <a v-if="item === '...'" class="page-number-circle">
            {{ item }}
          </a>
          <a v-else-if="currentpage===item.number" class="current-page">
            {{ item.number }}
          </a>
          <a v-else class="page-number-circle" @click="moveToPage(item.url)">
            {{ item.number }}
          </a>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!next"
            @click="moveToPage(next)"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
</template>
<script>
export default {
    name: "TablePaginator",
    props: {
        type: {
            type: String,
        },
        title: String,
        hostApi:{
            type: String,
            default: process.env.VUE_APP_SERVER_HOST
        },
        source: {
            type: String,
            default: "modicare"
        },
        next:{
            type: String,
            default: null
        },
        prev: {
            type: String,
            default: null
        },
        currentpage: {
            type: Number,
            default: 1
        },
        allPages: {
          // type: Array,
          default: [{
            number: 1,
            url: null
          }]
        }
    },
    data(){
      return {
        requirePages: [],
        loading: false
      }
    },
    watch: {
      currentpage: async function (value) {
        this.requirePages = await this.mapRequirePage(this.allPages, value);
    },
  },
    async mounted(){
      this.loading = true;
      setTimeout(async ()=>{
        this.requirePages = await this.mapRequirePage(this.allPages, this.currentpage);
        this.loading = false;
      }, 2000)
    },
    methods:{
        async moveToPage(page_url){
          this.$emit("jump-to-page",page_url)
        },
        async mapRequirePage(all_pages, current_page){
          const res = await this.showRequiredPage(all_pages.length, current_page, 6);
          const newPages =  res.map(page => {
            if(page === "..."){
              return page
            }else{
              return all_pages.find(page_=>page_.number===page)
            }
          })
          console.log("new pages", newPages);
          return newPages;
        },
        async showRequiredPage(totalPages, currentPage, maxVisiblePages){
          const visiblePages = [];
          if (totalPages <= maxVisiblePages) {
            // Show all pages if the total number of pages is less than or equal to maxVisiblePages
            visiblePages.push(...Array.from({ length: totalPages }, (_, index) => index + 1));
          } else {
            const halfMaxVisible = Math.floor(maxVisiblePages / 2);
            
            if (currentPage <= halfMaxVisible) {
              // Show pages from the beginning up to maxVisiblePages
              visiblePages.push(...Array.from({ length: maxVisiblePages }, (_, index) => index + 1));
            } else if (currentPage >= totalPages - halfMaxVisible) {
              // Show pages from (totalPages - maxVisiblePages + 1) up to totalPages
              visiblePages.push(...Array.from({ length: maxVisiblePages }, (_, index) => totalPages - maxVisiblePages + index + 1));
            } else {
              // Show pages around the current page
              const startPage = currentPage - halfMaxVisible;
              // const endPage = currentPage + halfMaxVisible;
              visiblePages.push(...Array.from({ length: maxVisiblePages }, (_, index) => startPage + index));
            }
          }

          // Add ellipsis if necessary
          if (visiblePages[0] > 1) {
            visiblePages.unshift('...');
          }
          if (visiblePages[visiblePages.length - 1] < totalPages) {
            visiblePages.push('...');
          }

          return visiblePages;
        }
    },
}
</script>
<style>
 .pagination {
        display: flex;
        padding-left: 0;
        list-style: none;
      }
      .pagination-btn {
        background-color: #e8e5e5;
        border: 0;
        padding: 10px 40px;
        border-radius: 61px;
      }
      .page-number-circle {
        display: inline-block;
        border-radius: 50%;
        min-width: 40px;
        min-height: 40px;
        text-align: center;
        padding: 8px;
        border: 1px solid #d6d6d6;
        cursor: pointer;
      }
      .current-page {
        background-color: #2699fb;
        color: white;
        display: inline-block;
        border-radius: 50%;
        min-width: 40px;
        min-height: 40px;
        text-align: center;
        padding: 8px;
        border: 1px solid #d6d6d6;
        pointer-events: none;
      }
</style>
