<template>
    <div class="add-addsense">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewBrochure}"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">View Brochure</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brochure Name
                          </label>
                         <input type="text" class="form-control" placeholder="Brochure Name" name="brochure_name" v-model="brochure_name">
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brochure Book Name
                          </label>
                          <input type="text" class="form-control" placeholder="book_name" name="book_name" v-model="book_name">
                       </div>
                    </div>
                  </div>
                  <div class="row">
                      <div class="col-6">
                          <div class="form-group has-label">
                              Brochure Book PDF
                          </div>
                          <a>{{ book_pdf_url }}</a>
                          <input type="file" class="form-control" placeholder="PDF" ref="book_pdf" accept="application/pdf" />
                      </div>
                  </div>
                </div>
            </div>

            <div class="card-footer">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="button" class="btn btn-info">Update Brochure</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
    input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";

export default {
    name: "Edit Brochure",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            brochure_name: "",
            book_name: "",
            book_pdf_url: "",
            ViewBrochure: ""
        }
    },
    async mounted() {
      if(this.source === "modicare"){this.ViewBrochure = "Brochure"}
      else{
        this.ViewBrochure = this.source+"Brochure";
      }
        let brochureId = this.$route.params.id;
        let res = await axios.get(this.hostApi+`/api/brochure/${brochureId}/`);
        this.brochure_name = res.data.brochure_name;
        this.book_name = res.data.book_name;
        this.book_pdf_url = res.data.book_pdf;
    },
    methods: {
        resetForm(){
            this.brochure_name = "";
            this.book_name = "";
        }
    }
}
</script>