<template>
  <div>
    <base-header type="gradient" class="pb-6 pb-8 pt-5 pt-md-8">
    </base-header>
    <div class="container-fluid mt--7">
        <div class="row">
            <div class="col-lg-8 mt-2">
            <span>
                <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
                </i>
                <router-link :to="{ name: CategoriesPage }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
            </span>
            </div>
        </div>
      <div class="row">
        <div class="col">
          <product-table title="Light Table" :hostApi="hostApi" :source="source"></product-table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ProductTable from "./ProductByCategory.vue"
export default {
  name: "Category Products",
  components: {
    ProductTable,
  },
  props: {
    source: {
      type: String,
      default: "modicare"
    },
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    }
  },
  data(){
    return {
        CategoriesPage: ''
    }
  },
  mounted() {
    if(this.source == "modicare"){
        this.CategoriesPage = "Categories"
      }else{
        this.CategoriesPage = this.source+"Categories"
      }
  }
};
</script>
