
<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Order Details
          </h3>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <table class="table tablesorter table align-items-center table-flush">
       <thead class="thead-light">
            <tr>
            <th>Order Number / Date</th>
            <th>Order Details</th>
            <th>Shipping Details</th>
            <th>User Contact Details</th>
            <th>Order Amount / Qtyy</th>
            </tr>
        </thead>
        <tbody class="list">
            <tr>
            <th>
                <div class="media-body">
                <h4 class="text-info">{{ orderDetails.orderNumber }}</h4>
                <div>{{ orderDetails.created_at }}</div>
                </div>
            </th>
            <td class="">
                <div>
                    <b>DP ID - {{ orderDetails.distributionpoint?.dpID }}</b>
                    <br>{{ orderDetails.distributionpoint?.dpName }}, {{ orderDetails.distributionpoint?.mob }},
                </div>
                <div>
                    {{ orderDetails.distributionpoint?.dpAddress }}<br>
                    {{ orderDetails.distributionpoint?.cityName }}, {{ orderDetails.distributionpoint?.state }},
                    {{ orderDetails.distributionpoint?.pincode }}
                </div>
            </td>
            <td class="">
                <div class="">
                    <b>{{ orderDetails.shippingDetails?.address }}, {{ orderDetails.shippingDetails?.mobileNumber }}</b>
                </div>
                <div class="mt-1">
                    {{ orderDetails.shippingDetails?.city }}, {{ orderDetails.shippingDetails?.state }}, {{ orderDetails.shippingDetails?.pincode }}
                </div>
            </td>
            <td class="">
                <div class="text-primary font-weight-bold mt-1">
                    Mob - {{ orderDetails.user?.mob }}
                </div>
                <div class="">
                    Email - {{ orderDetails.user?.email }}
                </div>
            </td>
            <td class="">
                <div class="text-primary font-weight-bold">
                    <h4>Rs. {{ calculateTotalamount(orderDetails.orderProducts)}} /-</h4>
                </div>
                <div class="text-primary font-weight-bold">
                    Total Products: {{ calculateLength(orderDetails.orderProducts) }}
                </div>
            </td>
        </tr>
        </tbody>
      </table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
    name: "get order",
    props: {
        type: {
        type: String,
        },
        title: String,
        hostApi: {
            type: String,
            default: process.env.VUE_APP_SERVER_HOST
        },
        source: {
            type: String,
            default:"modicare"
        }
    },
    data() {
        return {
            orderDetails:{},
            ChangeStatus:false,
            Status:'',
        }
    },
    async mounted() {
        let order_id = this.$route.params.id;
        let orderData = await axios.get(this.hostApi+`/api/order/${order_id}/`);
        this.orderDetails = orderData.data;
    },
    methods: {
         calculateTotalamount(dataArr) {
            if(dataArr !== undefined) {
                if(dataArr.length === 1){
                    return ((parseFloat(dataArr[0].PricePerPiece)) * dataArr[0].productQty).toFixed(2)
                }else{
                    let res = dataArr.map(item => parseFloat(item.PricePerPiece) * parseInt(item.productQty)).reduce((obj, acc)=>obj + acc)
                    return (res).toFixed(2);
                }  
            }
      },
      calculateLength(dataArr){
          if (dataArr !== undefined){
              return dataArr.length;
          }
      }
    }
}
</script>