<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Sections
          </h3>
        </div>
         <div class="col text-right">
          <base-button type="info" size="sm" @click="addVideoData()">Add Video</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Title</th>
          <th>Video Link</th>
          <th>Thumbnail</th>
          <th>Section</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
            <div class="align-center">
                <router-link :to="{ name: ViewVideoData, params:{id:row.item.id }}" > {{ row.item.title }} </router-link>
              </div>
          </th>

          <td class="text-align-center">
            <div class="media-body">
                <span class="name mb-0 text-sm font-weight-bold">
                  <!-- <router-link :to="{name: ViewSection, params:{id:row.item.id}}" >{{ row.item.section_name }}</router-link> -->
                  {{ row.item.video_link }}
                </span>
              </div>
          </td>

          <td class="text-align-center">
            <div class="media-body">
                <span class="name mb-0 text-sm font-weight-bold">
                  <!-- <router-link :to="{name: ViewSection, params:{id:row.item.id}}" >{{ row.item.section_name }}</router-link> -->
                  <img class="youtube-thumbnail" alt="Image placeholder" :src="row.item.thumbnail" />
                </span>
              </div>
          </td>
          <td class="text-align-center">
            <div class="media-body">
                <span class="name mb-0 text-sm font-weight-bold">
                  <!-- <router-link :to="{name: ViewSection, params:{id:row.item.id}}" >{{ row.item.section_name }}</router-link> -->
                  {{ row.item.section }}
                </span>
              </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
           <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewVideoData, params:{id: row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteVideoData(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import CustomLoader from "../../components/CustomLoader.vue";
import { deleteVideo, getVideoData } from "../../services/apiHandler";

export default {
    name: 'VideoTable',
    components: {
      CustomLoader
    },
    props: {
      type: {
        type: String,
      },
      title: String,
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            tableData:[],
            mainData:{},
            sectionData:[],
            perpage:8,
            currentpage:1,
            pagination: {
                default: 1
            },
            ViewVideoData:"",
            ViewAddVideo:"",
            loading: false
        }
    },
    async mounted() {
        if(this.source === "modicare"){
          this.ViewVideoData = "ViewVideoData";
          this.ViewAddVideo = "AddVideoData";
        }else{
          this.ViewVideoData = this.source+"ViewVideoData";
          this.ViewAddVideo = this.source+"AddVideoData";
        }
        this.loading = true;
        let videos = await getVideoData(this.hostApi)
        this.tableData = videos.data;
        this.loading = false;
        if(this.tableData.length == 0){
        this.$toast.error(`No Video Data Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
        });
      }
    },
    methods: {
      addVideoData(){
        this.$router.push({ name: this.ViewAddVideo })
      },
      async deleteVideoData(id){
        this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) =>{
            if(result.isConfirmed) {
                deleteVideo(this.hostApi, id).then(() =>{
                    this.$swal('Deleted!','Selected Video data has been deleted.','success');
                    this.tableData = this.tableData.filter((obj)=>{return obj.id !== id;})
                }).catch((e) =>{
                    this.$swal('Something went wrong!',e.response.data.detail || e.response.data.message || e.message,'danger');
                })
            } 
        })
      }
    }
}
</script>