<template>
    <div class="card shadow bg-secondary">
    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row align-items-center">
            <div class="col">
            <h3 class="mb-0">How To Collect BV</h3>
            </div>
        </div>
        </div>
    </div>
        <div class="card-body">
        <div class="">
            <div v-for="(item, index) in formData.youtube_link" :key="index">
                <InputField :label="`Youtube Link `+index" :inputData="item" :index="index" :UpdateFunc="UpdateYoutubeLink" :DeleteFunc="DeleteYoutubeLink" />
            </div>
            <AddData headline="YouTube Link" newData="newYoutubeLink" :addFunc="AddYoutubeLink" :showAdd="add_youtube_link" :enableAddition="AddYoutubeLinkToggle"/>
            <hr />
            <div v-for="(item, index) in formData.description" :key="index">
                <InputField :label="`Description `+index" :inputData="item" :index="index" :UpdateFunc="UpdateDescription" :DeleteFunc="DeleteDescription" />
            </div>
            <AddData headline="Description" newData="newDescription" :addFunc="AddDescription" :showAdd="add_description" :enableAddition="AddDescriptionToggle"/>
        </div>
        </div>
    </div>
</template>
<script>
import axios from "axios";
import AddData from "../../common/newData.vue";
import InputField from "../../common/inputField.vue";
export default {
    name: "Descriptions",
    components:{
        AddData,
        InputField
    },
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
    },
    data() {
        return {
            formData : {
               youtube_link: [],
               description: []
            },
            add_description:false,
            add_youtube_link: false,
        }
    },
    async mounted() {
        const descriptions = await axios.get(this.hostApi+'/api/howtocollectbv/')
        this.formData = descriptions.data.response;
    },
    methods:{
        AddDescriptionToggle(){
            this.add_description = !this.add_description;
        },
        AddYoutubeLinkToggle(){
            this.add_youtube_link = !this.add_youtube_link;
        },
        async addDataFunc(payload){
            const config = {
            method: 'post',
            url: this.hostApi+'/api/howtocollectbv/',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : payload
            };
            return await axios(config)
        },
        async AddDescription(event, desc_data){
            if(!desc_data)return;
            event.target.disabled = true;
            event.target.innerText = "Adding..."
            const payload = JSON.stringify({
                "description": desc_data
            });
            this.addDataFunc(payload).then(()=>{
                event.target.disabled = false;
                event.target.innerText = "Add"
                this.$toast.success(`Description added Successfully!`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                this.formData.description.push(desc_data)
                this.AddDescriptionToggle();
            }).catch((err)=>{
                this.$toast.error(`${err.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Add"
            })
        },
        async AddYoutubeLink(event, youtube_link){
            if(!youtube_link)return;
            event.target.disabled = true;
            event.target.innerText = "Adding..."
            const payload = JSON.stringify({
                "youtube_link": youtube_link
            });
            this.addDataFunc(payload).then(()=>{
                event.target.disabled = false;
                event.target.innerText = "Add"
                this.$toast.success(`YouTube Link added Successfully!`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                this.formData.youtube_link.push(youtube_link)
                this.AddYoutubeLinkToggle();
            }).catch((err)=>{
                this.$toast.error(`${err.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Add"
            })
        },
        async deleteHCB(payload){
            const config = {
            method: 'delete',
            url: this.hostApi+'/api/howtocollectbv/?removal_type=index',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : payload
            };
            return await axios(config);
        },
        async DeleteDescription(event, index){
            this.$swal({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) =>{
                if(result.isConfirmed){
                    event.target.disabled = true;
                    event.target.innerText = "Deleting..."
                    const payload = JSON.stringify({
                        "description": index
                    });
                    this.deleteHCB(payload).then(()=>{
                        this.formData.description.pop(index);
                        this.$toast.success(`Description Deleted Successfully`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    }).catch((err)=>{
                        event.target.disabled = false;
                        event.target.innerText = "Delete"
                        this.$toast.error(`${err.message}`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    })
                }
            })
        },
        async DeleteYoutubeLink(event, index){
            this.$swal({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) =>{
                if(result.isConfirmed){
                    event.target.disabled = true;
                    event.target.innerText = "Deleting..."
                    const payload = JSON.stringify({
                        "youtube_link": index
                    });
                    this.deleteHCB(payload).then(()=>{
                        this.formData.youtube_link.pop(index);
                        this.$toast.success(`YouTube Deleted Successfully`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    }).catch((err)=>{
                        event.target.disabled = false;
                        event.target.innerText = "Delete"
                        this.$toast.error(`${err.message}`,{
                            position: "top",
                            canTimeout:true,
                            duration:2500,
                        });
                    })
                }
            })
        },
        async updateHCB(payload){
            const config = {
                method: 'put',
                url: this.hostApi+'/api/howtocollectbv/?removal_type=index',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : payload
            };
            return await axios(config);
        },
        async UpdateDescription(event, description, index){
            event.target.disabled = true;
            event.target.innerText = "Updating..."
            const payload = JSON.stringify({
                "description_index": index,
                "description": description
            })
            this.updateHCB(payload).then(()=>{
                 this.$toast.success(`Description updated`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            }).catch((err)=>{
                this.$toast.error(`${err.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            })
        },
        async UpdateYoutubeLink(event, youtube_link, index){
            event.target.disabled = true;
            event.target.innerText = "Updating..."
            const payload = JSON.stringify({
                "youtube_link_index": index,
                "youtube_link": youtube_link
            })
            this.updateHCB(payload).then(()=>{
                 this.$toast.success(`Youtube Link updated`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            }).catch((err)=>{
                this.$toast.error(`${err.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
                event.target.disabled = false;
                event.target.innerText = "Update"
            })
        }
    }
}
</script>