<template>
  <div>
    <base-header type="gradient" class="pb-7 pt-7">
    </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-around">
        <div class="col-md-12 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewOrders }"   class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>

        <div class="col-md-12 mt-4 mb-8">
          <div class="row no-gutters justify-content-around">
            <div class="col-12">
                <get-order class="text-default" :hostApi="hostApi" :source="source"></get-order>
            </div>
            <div class="col-12 mt-4">
              <orderproduct-table title="Light Table" :hostApi="hostApi" :source="source"></orderproduct-table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import orderproductTable from "./OrderProductTable";
import getOrder from "./getOrder";

export default {
  name: "order tables",
  components: {
    getOrder,
    orderproductTable
  },
  data(){
    return {
      ViewOrders:""
    }
  },
  props: {
    hostApi:{
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  mounted() {
    if(this.source === "modicare"){this.ViewOrders = "Orders";}
    else{this.ViewOrders = this.source+"Orders"}
  }
};
</script>

