<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Order
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Order with Order Number"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <!-- <base-button type="info" size="sm"></base-button> -->
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading===true" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Order Number</th>
          <th>Order Date</th>
          <th>User</th>
          <th>DP Details</th>
          <th>Total Amount</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                    <router-link :to="{name: ViewOrderProduct, params:{id:row.item.id}}" >{{row.item.orderNumber }}</router-link>
                </span>
              </div>
          </th>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.created_at }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center" v-if="row.item.user.email">
                {{ row.item.user.email }}
            </div>
            <div v-else class="text-align-center text-warning">
              Unknown user
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold" v-if="row.item?.distributionpoint?.id">
                  <!-- <a :href="'/#/view/' + row.item.distributionpoint.id + '/distributionpoint'" ></a> -->
                  <router-link :to="{name: ViewDistributionpoint, params:{id: row.item.distributionpoint.id}}">{{ row.item.distributionpoint.dpID }}</router-link>
                </div>
                <div v-if="row.item?.distributionpoint?.id" class="">{{ row.item.distributionpoint.dpName }}</div>
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="font-weight-bold">Rs. {{ calculateTotalamount(row.item.orderProducts) }}</div>
                <div v-if="calculateLength(row.item.orderProducts) === 1" class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Product)
                </div>
                <div v-else class="font-italic">
                    ({{ calculateLength(row.item.orderProducts) }} Products)
                </div>
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:'OrderProduct', params:{id:row.item.id}}"
                >View Order Details</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="delOrder(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>

        </template>
      </base-table>
    </div>

    <TablePagination v-if="!loading" :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import { deleteOrder, getOrders, jumpToPage, searchingOrders } from "../../services/apiHandler";
import CustomLoader from "../../components/CustomLoader.vue";
import TablePagination from "../../components/Paginator/paginator.vue";

export default {
    name: 'orderTable',
    components:{
      CustomLoader,
      TablePagination
    },
    props: {
        type: {
            type: String,
        },
        title: String,
        hostApi: {
          type: String,
          default: process.env.VUE_APP_SERVER_HOST
        },
        source: {
          type: String,
          default: "modicare"
        },
    },
    data() {
        return {
            tableData: [],
            orderProducts: {},
            totalPriceData: {},
            perPage:8,
            currentpage:1,
            next:null,
            prev:null,
            all_pages: [],
            searchQuery:"",
            ViewDistributionpoint:"",
            ViewOrderProduct:"",
            loading: false
        }
    },
    async mounted(){
      if(this.source == "modicare"){
        this.ViewDistributionpoint = "ViewDistributionpoint";
        this.ViewOrderProduct = "OrderProduct";
      }else{
        this.ViewDistributionpoint = this.source+"ViewDistributionpoint";
        this.ViewOrderProduct = this.source+"OrderProduct"
      }
      this.loading = true;
      let orders = await getOrders(this.hostApi);
      await this.MapData(orders);
      this.loading = false;

  },
  methods: {
      calculateTotalamount(dataArr) {
          if(dataArr.length === 1){
              return ((parseFloat(dataArr[0].PricePerPiece)) * dataArr[0].productQty).toFixed(2)
          }else if(dataArr.length === 0){
              return;
          }
          else{
            let res = dataArr.map(item => parseFloat(item.PricePerPiece) * parseInt(item.productQty)).reduce((obj, acc)=>obj + acc)
            return (res).toFixed(2);
          }  
      },
      calculateLength(dataArr){
          return dataArr.length;
      },
      async searchTable(){
        this.loading = true;
        let orders = await searchingOrders(this.hostApi, this.searchQuery);
        this.loading = false;
        if(orders.data.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
        await this.MapData(orders);
      },
      async getNextPrevPageData(url){
        this.loading = true;
        const result = await jumpToPage(url)
        this.loading = false;
        await this.MapData(result);
      },
      async delOrder(order_id){
        this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) =>{
            if(result.isConfirmed) {
                deleteOrder(this.hostApi, order_id).then(() =>{
                  this.$swal('Deleted!','selected Order Details has been deleted.','success');
                  this.tableData.results = this.tableData.results.filter((obj)=>{return obj.id !== order_id;});
                }).catch((e) =>{
                  this.$swal('Something went wrong!',e.message,'danger');
                })
            } 
          })
      },
      async MapData(result){
        this.tableData = result.data.results;
        this.next = result.data.next;
        this.prev = result.data.previous;
        this.all_pages = result.data.all_pages;
        this.currentpage = result.data.current_page;
      }
  }
}
</script>