<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            User List
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search User With UserID, Name, email, Address"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addProduct()" size="sm">Add User</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading"/>
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>User Unique Id</th>
          <th>First Name</th>
          <th>Last Name</th>
          <th>Mobile Number</th>
          <th>Email</th>
          <th>Address</th>
          <th>Profile Picture</th>
          <th>Status</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <router-link :to="{name: ViewUserCoin, params:{id:row.item.user}}" >{{ row.item.user }}</router-link>
              </div>
          </th>
          <td class="text-align-center">
            <div v-if="row.item.first_name" class="align-center">
                {{ row.item.first_name }}
            </div>
            <div v-else class="align-center text-warning">
                *not given
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.last_name" class="align-center">
                {{ row.item.last_name }}
            </div>
            <div v-else class="align-center text-warning">
                *not given
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.mob }}
            </div>
          </td>
           <td class="text-align-center">
            <div v-if="row.item.email" class="align-center">
                {{ row.item.email }}
            </div>
            <div v-else class="align-center text-warning">
                *not given
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.address" class="align-center">
                {{ row.item.address }}
            </div>
            <div v-else class="align-center text-warning">
                *not given
            </div>
          </td>
           <td>
            <div class="avatar-group">
              <a
                href="#"
                class="avatar avatar-sm rounded-circle"
                data-toggle="tooltip"
                data-original-title="Jessica Doe"
              >
                <img
                  alt="Image placeholder"
                  class="rounded-circle"
                  :src="row.item.profile_picture"
                />
              </a>
            </div>
          </td>
           <td class="text-align-center">
            <base-switch :value="row.item[user_type]" v-on:change="changeStatus($event, row.item.id)"/>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteUser(row.item.user)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>
    <TablePagination v-if="all_pages.length > 0" :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import { changeUserStatus, deleteUserPermanently, jumpToPage, getUsersData, searchingUser } from "../../services/apiHandler";
import TablePagination from "../../components/Paginator/paginator.vue";
import CustomLoader from "../../components/CustomLoader.vue";
export default {
  name: "user-table",
  components:{
    TablePagination,
    CustomLoader
  },
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi:{
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  data() {
    return {
      tableData: [],
      next: null,
      prev: null,
      perpage:8,
      currentpage:1,
      all_pages:[],
      searchQuery:"",
      ViewUserCoin: "",
      user_type: '',
      loading: false
    };
  },
  async mounted() {
    if(this.source == "modicare"){
      this.ViewUserCoin = "UserCoin";
    }else{
      this.ViewUserCoin = this.source+"UserCoin"
    }
    this.loading = true;
    let user_lists = await getUsersData(this.hostApi);
    await this.MapData(user_lists);
    this.loading = false;
  },
  async created(){
    switch(this.source){
      case "Vestige":
        this.user_type = "vestige_user";
        return
      case "hhi":
        this.user_type = "hhi_user";
        return
      case "proteinWorld":
        this.user_type = "protein_world_user";
        return
      case "amulyaHerbal":
        this.user_type = "AmulyaHerbal_user";
        return
      default:
        this.user_type = "modicare_user"
    }
  },
  methods:{
      addUser(){
        alert("Work in Progress")
      },
      async searchTable(){
        this.loading = true;
        const user_lists = await searchingUser(this.hostApi, this.searchQuery);
        this.loading = false;
        if(user_lists.data.length == 0){
          this.$toast.error(`No Results Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
        await this.MapData(user_lists);
      },
      async changeStatus(e, user_id){
        try {
          const data = JSON.stringify({
            [this.user_type]: e.target.checked
          });
          await changeUserStatus(this.hostApi, data, user_id);
          this.$swal('Updated!','Selected User status altered','success');
        } catch (e) {
          this.$swal('Something went wrong!',e.message,'danger');
        }
      },

      async deleteUser(user_id){
        if(!user_id){
          return this.$swal('Something went wrong!',"User Id can't be empty!",'danger');
        }
        this.$swal({
            title: 'Are you sure?',
            text: "User will be deleted permanently!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) =>{
            if(result.isConfirmed) {
              deleteUserPermanently(user_id).then(()=>{
                this.$swal('Deleted!','Selected User has deleted permanently.','success');
              }).catch((e)=>{
                this.$swal('Something went wrong!',e.message,'danger');
              })
            } 
        })
      },
      async getNextPrevPageData(url){
        this.loading = true;
        const result = await jumpToPage(url);
        this.loading = false;
        await this.MapData(result);
      },
      async MapData(result){
        this.tableData = result.data.results;
        this.next = result.data.next;
        this.prev = result.data.previous; 
        this.currentpage = result.data.current_page;
        this.all_pages = result.data.all_pages;
      },
  }
};
</script>
<style></style>
