<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewDP }" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add DistributionPoint</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form @submit="addDP()"> 
                <h6 class="heading-small text-muted mb-4">DP Contact Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             DP ID
                          </label>
                            <input
                              placeholder="DP ID"
                              id="dpId"
                              v-model="dpID"
                              class="form-control form-control-alternative"
                              required
                              type="text"
                            />
                        </div>
                      </div>

                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Type
                            </label>
                            <select class="form-control form-control-alternative" v-model="dpType">
                              <option disabled value="">Please select one</option>
                              <option
                                v-for="item in Object.keys(dp_types)"
                                :key="item"
                                :value="item"
                              >
                                {{ dp_types[item] }}
                              </option>
                            </select>
                         </div>
                      </div>

                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Owner
                            </label>
                            <input
                              placeholder="DP Owner"
                              id="dpName"
                              v-model="dpName"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Mobile Number
                            </label>
                            <input
                              placeholder="Mobile Number"
                              id="mobileNumber"
                              required
                              v-model="mobileNumber"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Alt Mobile Number
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="altMobNum"
                              v-model="altMobNumber"
                              placeholder="Alt Mobile Number"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                              Email Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="emailAddress"
                              placeholder="Email Address"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Address Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               DP Address
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="dpAddress"
                              placeholder="DP Address"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               City
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              v-model="cityName"
                              placeholder="City Name"
                              required
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               State
                            </label>
                            <input
                              placeholder="State"
                              v-model="state"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               PinCode
                            </label>
                            <input
                              placeholder="PinCode"
                              v-model="pincode"
                              required
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">DP Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-4">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-outline-primary product-btn-cancel" @click="resetForm()">Cancel</button>
                    <button
                      type="submit"
                      v-on:click="addDP()"
                      class="btn btn-info"
                      >Add DP</button
                    >
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import { addDistributionPoint } from "../../services/apiHandler";

export default {
    name: 'AddDistributionPoint',
    props: {
      source: {
        type: String,
        default: "modicare"
      },
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      }
    },
    data() {
        return {
            dpID:"",
            dp_types:{},
            dpType:"",
            dpName: "",
            dpAddress:"",
            mobileNumber:"",
            altMobNumber:"",
            cityName:"",
            state:"",
            pincode:"",
            Status:true,
            ViewDP:''
        }
    },
    async mounted(){
      if(this.source === "modicare"){
        this.ViewDP = "DistributionPoint";
      }else{
        this.ViewDP = this.source+"DistributionPoint";
      }
    },
    async created(){
      if(this.source === "modicare"){
        this.dp_types = this.$store.state.dp_types;
      }else if(this.source === "hhi"){
        this.dp_types = this.$store.state.hhi_dp_types;
      }else if(this.source === "Vestige"){
        this.dp_types = this.$store.state.vestige_dp_types;
      }else if(this.source === "amulyaHerbal"){
        this.dp_types = this.$store.state.amulya_herbal_dp_types;
      }else if(this.source === "proteinWorld"){
        this.dp_types = this.$store.state.protein_world_dp_types;
      }
    },
    methods:{
        async addDP(){
          try{
            const { dpID, dpType, dpName, dpAddress, mobileNumber, emailAddress, altMobNumber, cityName, state, pincode, Status} = this;
            if(!dpID || !dpName || !dpAddress || !mobileNumber || !cityName || !state || !pincode || !emailAddress){
                this.$toast.error(`Please fill all the required field`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
            var data = {
                "dpID": dpID,
                "dp_type":dpType,
                "dpName": dpName,
                "dpAddress": dpAddress,
                "cityName": cityName,
                "pincode": pincode,
                "state": state,
                "mob": mobileNumber,
                "alternative_mob": altMobNumber,
                "emailAddress": emailAddress,
                "status": Status
            }
          await addDistributionPoint(this.hostApi, data);
          this.$toast.success(`Distribution Point added successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
          this.resetForm();
          }catch(e){
            this.$toast.error(`${e.response.data.detail || e.response.data.message}`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
          }
        },
        resetForm(){
            this.dpID="";
            this.dpName= "";
            this.dpAddress="";
            this.mobileNumber="";
            this.altMobNumber="";
            this.cityName="";
            this.state="";
            this.pincode="";
        },
    }
}
</script>