<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Products
          </h3>
        </div>
        <div class="form-group mb-0 col-4 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Product Code, Brand or Product Name"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />

          </div>
        </div>
        <!-- <div class="col-4 mt-2"> -->
          <div class="col-2">
            <select @change="filterTable()" v-model="Loyalty" class="form-control filter-dropdown ml-2">
            <option value="">All Loyalty</option>
            <option value="true">Loyalty: True</option>
            <option value="false">Loyalty: False</option>
          </select>
          </div>
         <div class="col-2">
           <select @change="filterTable()" v-model="Status" class="form-control filter-dropdown ml-2">
            <option value="">All Status</option>
            <option value="true">Status: Active</option>
            <option value="false">Status: InActive</option>
          </select>
         </div>
        <!-- </div> -->

        <div class="col text-right">
          <base-button type="info" @click="addProduct()" size="sm">Add Product</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading"/>
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Product Code</th>
          <th>Product Name</th>
          <th>Product Price</th>
          <th>Brand Name</th>
          <th>Loyalty</th>
          <th>Status</th>
          <th>Quantity</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name: ViewProduct, params:{id:row.item.id}}" >{{row.item.productCode }}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ this.formatData(row.item.productName) }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
               Rs. {{parseInt(row.item.productPrice).toFixed(2)}}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ this.formatData(row.item.brand.BrandName) }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.loyalty" class="text-success">True</div>
            <div v-else class="text-danger">False</div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.quantity }}
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewProduct, params:{id: row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="delProduct(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>
    <TablePagination v-if="all_pages.length > 0" :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import TablePagination from "../../components/Paginator/paginator.vue";
import { deleteProduct, filteringProducts, getProducts, jumpToPage, searchingProducts } from "../../services/apiHandler";
import CustomLoader from "../../components/CustomLoader.vue";
export default {
  name: "product-table",
  components:{
    TablePagination,
    CustomLoader
},
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  data() {
    return {
      tableData: [],
      all_pages: [],
      next:null,
      prev:null,
      perpage:1,
      currentpage:1,
       pagination: {
        default: 1
      },
      brand_lists:[],
      BrandID:"",
      Status:"",
      Loyalty:"",
      searchQuery:"",
      ViewProduct:"",
      loading: false
    };
  },
  async mounted(){
      if(this.source === "modicare"){
        this.ViewProduct = "ViewProduct";
      }else{
        this.ViewProduct = this.source+"ViewProduct";
      }
      this.loading = true;
      let products = await getProducts(this.hostApi);
       if(products.data.count == 0){
        this.$toast.error(`No Products Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
        });
      }
      this.MapData(products);
      this.loading = false;
  },
  methods:{
        addProduct(){
          if(this.source === "modicare"){
            this.$router.push('/addproduct');
          }else{
            this.$router.push(`/${this.source.toLowerCase()}/addproduct`);
          }
        },
        async delProduct(product_id){
          this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) =>{
            if(result.isConfirmed) {
              deleteProduct(this.hostApi, product_id).then(() =>{
                  this.tableData.results = this.tableData.results.filter((obj)=>{return obj.id !== product_id;});
                  this.$swal('Deleted!','selected Product has been deleted.','success');
                }).catch((e) =>{
                  this.$swal('Something went wrong!',e.response.data.detail,'danger');
                })
            } 
          })
        },
        async searchTable(){
          this.loading = true;
          let products = await searchingProducts(this.hostApi, this.searchQuery);
          this.loading = false;
          if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          await this.MapData(products);
        },
        async filterTable(){
          let loyalty = this.Loyalty;
          let status = this.Status;
          if(loyalty == "true"){loyalty = true}else if(loyalty == "false"){loyalty = false}
          if(status == "true"){status = true}else if(status == "false"){status = false}
          this.loading = true;
          let products = await filteringProducts(this.hostApi, loyalty, status);
          this.loading = false;
          if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          await this.MapData(products);
        },
        async getNextPrevPageData(url){
          this.loading = true;
          const result = await jumpToPage(url)
          this.loading = false;
          await this.MapData(result);
        },
      formatData(product_name){
        if(product_name.length > 15){
          return product_name.slice(0,15) + "...";
        }else{
          return product_name;
        }
      },
      async MapData(result){
        this.tableData = result.data.results;
        this.next = result.data.next;
        this.prev = result.data.previous; 
        this.currentpage = result.data.current_page;
        this.all_pages = result.data.all_pages;
      },
  }
};
</script>
