<template>
    <div class="mx-auto container" v-html="privacy_policy">
    </div>
</template>
<style scoped>
</style>
<script>
import axios from "axios";

export default {
    name: 'Privacy Policy',
    data () {
        return {
            privacy_policy:''
        }
    },
    async mounted() {
        let staticData = await axios.get(process.env.VUE_APP_SERVER_HOST+"/api/staticdata/");
        console.log(staticData)
        this.privacy_policy = staticData.data[0].privacy_policy;
    }
}
</script>