<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Sections
          </h3>
        </div>
         <div class="col text-right">
          <base-button type="info" size="sm" @click="addSection()">Add Section</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="sectionData"
      >
        <template v-slot:columns>
          <th>Title</th>
          <th>Section Type</th>
          <th>Section Name</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
            <div class="align-center">
               <div v-for="data in mainData[row.item.section_name]" :key="data"> 
                <router-link :to="{name: ViewSection, params:{id:data.id}}" >{{data.title}}</router-link>
              </div>
            </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
               <div v-for="data in mainData[row.item.section_name]" :key="data"> 
                {{data.section_type}}
              </div>
            </div>
          </td>

          <td class="text-align-center">
            <div class="media-body">
                <span class="name mb-0 text-sm font-weight-bold">
                  <!-- <router-link :to="{name: ViewSection, params:{id:row.item.id}}" >{{ row.item.section_name }}</router-link> -->
                  {{ row.item.section_name }}
                </span>
              </div>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import CustomLoader from "../../components/CustomLoader.vue";
import { getSectionData } from "../../services/apiHandler";

export default {
    name: 'SectionTable',
    components:{
      CustomLoader
    },
    props: {
      type: {
        type: String,
      },
      title: String,
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            tableData:[],
            mainData:{},
            sectionData:[],
            perpage:8,
            currentpage:1,
            pagination: {
                default: 1
            },
            ViewSection:"",
            ViewAddSection:"",
            loading: false
        }
    },
    async mounted() {
        if(this.source === "modicare"){
          this.ViewSection = "ViewSection";
          this.ViewAddSection = "AddSection";
        }else{
          this.ViewSection = this.source+"ViewSection";
          this.ViewAddSection = this.source+"AddSection";
        }
        this.loading = true;
        let sections = await getSectionData(this.hostApi);
        let homeSection = {}
        sections.data.forEach((item)=>{
          if(item.section_name in homeSection){
            homeSection[item.section_name].push(item)
          }else{
            homeSection[item.section_name]=[]
            this.sectionData.push({'section_name':item.section_name,'id':item.id,'type':item.section_type})
            homeSection[item.section_name].push(item)
          }
        })
        this.loading = false
        // console.log(homeSection)
        this.tableData = sections.data;
        this.mainData = homeSection
    },
    methods: {
      addSection(){
        this.$router.push({name: this.ViewAddSection })
      }
    }
}
</script>