<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Distribution Point
          </h3>
        </div>
        <div class="form-group mb-0 col-6 mt-md-2">
          <div class="form-group input-group-alternative input-group" style="border: none; margin-bottom: 0px; border-radius: 58px" >
            <input
              aria-describedby="addon-right addon-left"
              placeholder="Search Ditribution Point With DPID, Name, State, City or Address"
              v-on:keyup.enter="searchTable()"
              class="form-control search-box"
              v-model="searchQuery"
            />
          </div>
        </div>
        <div class="col">
           <select v-on:change="sortTableData()" class="form-control form-control-alternative" v-model="dpType">
              <option disabled value="">Sort By</option>
              <option
                v-for="item in Object.keys(dp_types)"
                :key="item"
                :value="item"
              >
                {{ dp_types[item] }}
              </option>
            </select>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addDistributionPoint()" size="sm">Add Distribution Point</base-button>
        </div>
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading" />
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>DP ID</th>
          <th>DP Type</th>
          <th>DP Owner</th>
          <th>DP Address</th>
          <th>Mob</th>
          <th>Email</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name: ViewDP, params:{id:row.item.id}}" >{{row.item.dpID }} </router-link>
                </span>
              </div>
          </th>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.dp_type }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.dpName }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div class="dp_address">{{ row.item.dpAddress }}</div>
                <div class="">City - {{ row.item.cityName }}, State - {{ row.item.state}}</div>
                <div class="">PinCode - {{ row.item.pincode }}</div>
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.mob }}
            </div >
            <div v-if="row.item.alternative_mob">
              Alt Mob - {{ row.item.alternative_mob}}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.emailAddress" class="align-center">
                {{ row.item.emailAddress }}
            </div>
            <div v-else class="align-center">Not given*</div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name: ViewDP, params:{id: row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteDP(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>
    <TablePagination :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<style scoped>
  .dp_address{
    max-width: 300px;
    min-width: 150px;
    overflow: hidden;
  }
</style>
<script>
import axios from "axios";
import { deleteDistributionPoint, getDistributionPoint, jumpToPage, searchingDistributionPoint, sortDistributionPoint } from "../../services/apiHandler";
axios.defaults.xsrfHeaderName = "X-CSRFToken";
import CustomLoader from "../../components/CustomLoader.vue";
import TablePagination from "../../components/Paginator/paginator.vue";

export default {
  name: "dp-table",
  components:{
    CustomLoader,
    TablePagination
  },
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  data(){
      return {
          tableData: [],
          dp_types:{},
          dpType:"",
          searchQuery:"",
           pagination: {
            default: 1
          },
          viewAddDP:'',
          ViewDP:'',
          currentpage: 1,
          next: null,
          prev: null,
          all_pages:[],
          loading: false
      }
  },
  async mounted() {
      if(this.source === "modicare"){
        this.viewAddDP = "AddDistributionPoint";
        this.ViewDP = "ViewDistributionpoint"
      }else{
        this.viewAddDP = this.source+"AddDistributionPoint";
        this.ViewDP = this.source+"ViewDistributionpoint"
      }
      this.loading = true;
      let d_points = await getDistributionPoint(this.hostApi);
      await this.MapData(d_points);
      this.loading = false;
  },
  async created(){
    this.dp_types = this.$store.state.dp_types;
  },
  methods: {
    addDistributionPoint(){
      this.$router.push({name:this.viewAddDP});
    },
    async deleteDP(dp_id){
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) =>{
        if(result.isConfirmed) {
          deleteDistributionPoint(this.hostApi, dp_id).then(() =>{
              this.$swal('Deleted!','Your file has been deleted.','success');
            }).catch((e) =>{
              this.$swal('Something went wrong!',e.response.data.detail || e.response.data.message,'danger');
            })
        } 
      })
    },
    async searchTable(){
      this.loading = true;
      let d_points = await searchingDistributionPoint(this.searchQuery);
      if(d_points.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
        this.loading = false;
      await this.MapData(d_points)
    },
    async sortTableData(){
      this.loading = true;
      let d_points = await sortDistributionPoint(this.hostApi, this.dpType);
      if(d_points.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
          });
        }
      this.loading = false;
      await this.MapData(d_points)
    },
    async getNextPrevPageData(url){
      this.loading = true;
      const result = await jumpToPage(url, true)
      await this.MapData(result);
      this.loading = false;
    },
    async MapData(result){
      this.tableData = result.data.results;
      this.next = result.data.next;
      this.prev = result.data.previous; 
      this.currentpage = result.data.current_page;
      this.all_pages = result.data.all_pages;
    }
  }
}
</script>
