<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center no-gutters">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Brochure
          </h3>
        </div>
        <div class="col text-right">
          <base-button type="info" @click="addBrochure()" size="sm">Add Brochure</base-button>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Brochure Name</th>
          <th>Brochure Book Name</th>
          <th>Brochure Book PDF</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm" v-if="row.item.brochure !== null">
                    <router-link :to="{name:ViewBrochure, params:{id:row.item.id}}">{{ row.item.brochure.brochure_name }}</router-link>
                </span>
                <span class="name mb-0 text-sm" v-else>
                    <router-link :to="{name:ViewBrochure, params:{id:row.item.id}}">"Brochure to be added</router-link>
                </span>

              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.book_name }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                <div v-if="row.item.book_pdf !== null">
                    <a :href="row.item.book_pdf" target="_blank" rel="noopener">Open</a>
                </div>
                <div v-else class="text-warning">
                    No PDF Added
                </div>
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewBrochure, params:{id:row.item.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteBrochure(row.item.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>

        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
      <base-pagination :page-count="10" v-model="pagination.default"></base-pagination>
    </div>
  </div>
</template>
<script>
import axios from "axios";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default {
  name: "brochure-table",
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
  },
  data() {
    return {
      tableData: [],
      perpage:8,
      currentpage:1,
       pagination: {
        default: 1
      },
      ViewBrochure:"",
      ViewAddBrochure:""
    };
  },
  async mounted() {
    if(this.source == "modicare"){
      this.ViewBrochure = "ViewBrochure";
      this.ViewAddBrochure = "AddBrochure";
    }else{
      this.ViewBrochure = this.source+"ViewBrochure"
      this.ViewAddBrochure = this.source+"AddBrochure";
    }
      let brochure = await axios.get(this.hostApi+"/api/brochure/");
      this.tableData = brochure.data;
  },
  methods:{
    addBrochure(){
        this.$router.push({name: this.ViewAddBrochure });
    },
    async deleteBrochure(brochure_id){
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) =>{
        if(result.isConfirmed) {
           var config = {
              method: 'delete',
              url: this.hostApi+'/api/brochure/'+ brochure_id +'/',
              xsrfHeaderName: "X-CSRFToken",
            };
            axios(config).then(() =>{
              this.$swal('Deleted!','Selected Brochure has been deleted.','success');
              this.tableData = this.tableData.filter((obj)=>{return obj.id !== brochure_id;})
            }).catch((e) =>{
              this.$swal('Something went wrong!',e.message,'danger');
            })
        } 
      })
    }
  }
};
</script>
<style></style>
