<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Products of {{ CategoryName }}
          </h3>
        </div>
        <!-- <div class="col-4 mt-2"> -->
        <!-- </div> -->
      </div>
    </div>
    <CustomLoader v-if="loading" :isLoading="loading"/>
    <div v-else class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData"
      >
        <template v-slot:columns>
          <th>Product Code</th>
          <th>Product Name</th>
          <th>Product Price</th>
          <th>Brand Name</th>
          <th>Loyalty</th>
          <th>Status</th>
          <th>Quantity</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name: ViewProduct, params:{id:row.item.product.id}}" >{{row.item.product.productCode }}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.productName }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
               Rs. {{parseInt(row.item.product.productPrice).toFixed(2)}}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.brand.BrandName }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.loyalty" class="text-success">True</div>
            <div v-else class="text-danger">False</div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.product.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.quantity }}
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewProduct, params:{id: row.item.product.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteProduct(row.item.product.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>
    <TablePagination :next="next" :prev="prev" :allPages="all_pages" :currentpage="currentpage" @jump-to-page="getNextPrevPageData" />
  </div>
</template>
<script>
import axios from "axios";
import { getProductByCategory, jumpToPage } from "../../services/apiHandler";
import CustomLoader from "../../components/CustomLoader.vue";
import TablePagination from "../../components/Paginator/paginator.vue";

export default {
  name: "category-product-table",
  components: {
    CustomLoader,
    TablePagination
  },
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  data() {
    return {
      tableData: [],
      perpage:1,
      currentpage:1,
      all_pages: [],
      next: null,
      prev: null,
      brand_lists:[],
      BrandID:"",
      CategoryName:"",
      Status:"",
      Loyalty:"",
      searchQuery:"",
      ViewProduct:"",
      loading: false
    };
  },
  async mounted(){
      if(this.source === "modicare"){
        this.ViewProduct = "ViewProduct";
      }else{
        this.ViewProduct = this.source+"ViewProduct";
      }
      this.loading = true;
      let products = await getProductByCategory(this.hostApi, this.$route.params.id) //axios.get(this.hostApi+"/api/tagpivot/?category="+this.$route.params.id);
      if(products.data.count == 0){
        this.$toast.error(`No Products Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
        });
      }
      await this.MapData(products);
      let category = await axios.get(this.hostApi+"/api/tags/"+this.$route.params.id+"/")
      this.CategoryName = category.data.tagName;
      this.loading = false;
  },
  methods:{
      async getNextPrevPageData(url){
        this.loading = true;
        const result = await jumpToPage(url)
        this.loading = false;
        await this.MapData(result);
      },
      async MapData(result){
        this.tableData = result.data.results;
        this.next = result.data.next;
        this.prev = result.data.previous; 
        this.currentpage = result.data.current_page;
        this.all_pages = result.data.all_pages;
      },
  }
};
</script>
