<template>
    <div class="add-addsense">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewBrochure }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add Brochure</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brochure Name
                          </label>
                         <select class="form-control form-control-alternative" v-model="brochure">
                              <option disabled value="">Please select one</option>
                              <option
                                v-for="item in brochures"
                                :key="item.id"
                                :value="item.id"
                                >
                              {{ item.brochure_name }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brochure Book Name
                          </label>
                          <input type="text" class="form-control" placeholder="book_name" name="book_name" v-model="book_name">
                       </div>
                    </div>
                  </div>
                  <div class="row">
                      <div class="col-6">
                          <div class="form-group has-label">
                              Brochure Book PDF
                          </div>
                          <input type="file" class="form-control" placeholder="PDF" ref="book_pdf" accept="application/pdf" />
                      </div>
                  </div>
                </div>
            </div>

            <div class="card-footer">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="addBrochure()">Add Brochure</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add Brochure Section</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brochure Name
                          </label>
                         <input type="text" class="form-control" placeholder="Brochure Name" name="brochure_name" v-model="brochure_name">
                       </div>
                    </div>
                    <div class="col-4">
                          <div class="form-group has-label">
                              <label class="form-control-label">
                                Brochure Image
                              </label>
                          </div>
                          <div class="card">
                            <div v-if="image_url">
                              <img class="card-img-top" :src="image_url" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="brochure_image" type="file" v-on:change="handleImageUpload()" accept="image/*"/>
                              <button type="button" v-if="!image_url" class="btn btn-primary btn-sm" @click="$refs.brochure_image.click()">Add</button>
                              <button type="button" v-else class="btn btn-primary btn-sm" @click="$refs.brochure_image.click()">Change</button>
                              <button type="button" :disabled="!image_url" class="btn btn-info btn-sm" @click="removeImage()">Remove</button>
                            </div>
                          </div>
                      </div>
                  </div>
                </div>
            </div>

            <div class="card-footer">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm2()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="addBrochureSection()">Add Brochure Section</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
    max-height: 172px;;
  }
  .card-img-top{
    min-height: 172px;;
  }
</style>
<script>
import axios from "axios";

export default {
    name: "Add Brochure",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return {
            brochures:[],
            brochure:"",
            brochure_name: "",
            book_name: "",
            image_url:"",
            ViewBrochure:""
        }
    },
    async mounted() {
        if(this.source === "modicare"){
          this.ViewBrochure = "Brochure";
        }else{
          this.ViewBrochure = this.source+"Brochure";
        }
        let res = await axios.get(this.hostApi+'/api/brochure-section/');
        this.brochures = res.data;
    },
    methods: {
        async addBrochure(){
            try{
                let form = new FormData();
                form.append("brochure", this.brochure);
                form.append("book_name", this.book_name);
                form.append("brochure_image", this.$refs.book_pdf.files[0]);
                const config = {
                    method: 'post',
                    url: this.hostApi+'/api/brochure/',
                    headers: { 
                    'Content-Type': 'multipart/form-data'
                    },
                    data : form,
                    xsrfHeaderName: "X-CSRFToken",
                };
                let res = await axios(config)
                if(res.status == 201){
                    this.$toast.success(`Brochure added successfully!`,{
                        position: "top",
                        canTimeout:true,
                        duration:2500,
                    });
                    this.resetForm()
                }else{
                     this.$toast.error(`Something went wrong`,{
                        position: "top",
                        canTimeout:true,
                        duration:2500,
                    });
                }
            }catch(e) {
                 this.$toast.error(`${e.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        },
        async addBrochureSection(){
            try{
                let form = new FormData();
                form.append("brochure_name", this.brochure_name);
                if(this.image_url){
                    form.append("brochure_image", this.$refs.brochure_image.files[0]);
                }
                const config = {
                    method: 'post',
                    url: this.hostApi+'/api/brochure-section/',
                    headers: { 
                    'Content-Type': 'multipart/form-data'
                    },
                    data : form,
                    xsrfHeaderName: "X-CSRFToken",
                };
                let res = await axios(config);
                if(res.status === 201){
                     this.$toast.success(`new Image added successfully!`,{
                        position: "top",
                        canTimeout:true,
                        duration:2500,
                    });
                    this.brochures.push(res.data);
                    this.resetForm2()
                }else{
                    this.$toast.error(`Something went wrong`,{
                        position: "top",
                        canTimeout:true,
                        duration:2500,
                    });
                }
            }catch (e) {
                this.$toast.error(`${e.message}`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        },
        resetForm(){
            this.brochure = "";
            this.book_name = "";
        },
        resetForm2(){
            this.brochure_name = "";
            this.image_url = "";
        },
        handleImageUpload(){
            let brochureImage = this.$refs.brochure_image.files;
            this.image_url = URL.createObjectURL(brochureImage[0]);
        },
        removeImage(){
            this.image_url = "";
        }
    }
}
</script>